<template>
    <div class="more-view">
        <img :src="msg" alt="">
    </div>
</template>

<script>
export default{
  data () {
    return {
      msg: undefined
    }
  },
  created () {
    this.getProduct()
    this.$parent.head.isWhite = false
    this.$parent.head.isEnter = false
    this.$parent.head.title = '查看更多'
    this.$parent.head.isArrow = false
  },
  methods: {
    getProduct () {
      this.msg = this.$route.params.msg
      console.log(this.msg)
    }
  }
}
</script>

<style lang="scss" scoped>
.more-view {
  @include center($head-h, 0);
  img{
      width: 100vw;
      height: 100vh;
  }
}
</style>



