<template>
  <div class="body">
    <use-code :code='code1'></use-code>
    <use-code :code='code2'></use-code>
    <use-code :code='code3'></use-code>
    <button class="real" @click="real">确认</button>
     <transition name=fade>
    <p class="msg msg-x" v-show="isTwo">两次密码不一致</p>
     </transition>
      <transition name=fade>
    <p class="msg msg-x" v-show="isOld">旧密码错误</p>
      </transition>
  </div>
</template>

<script>
import useCode from '../useCode'
export default {
  data () {
    return {
      code1: {
        title: '输入原密码',
        main: ''
      },
      code2: {
        title: '输入新密码',
        main: ''
      },
      code3: {
        title: '确认新密码',
        main: ''
      },
      isTwo: false,
      isOld: false
    }
  },
  methods: {
    real: function () {
      if (this.code1.main && this.code2.main && this.code3.main) { // 三行都填的时候
        if (this.code2.main !== this.code3.main) { // 两个现在的密码不一样的时候弹窗
          this.isTwo = true
          setInterval(
            () => { this.isTwo = false }, 2000)
        } else { // 一样的时候请求
          let a = {
            password: this.code1,
            newPassword: this.code2
          }
          this.$http(this.path.myChangeCode(123, a))
            .then((rex) => {
              if (rex.data.code === 1) {
                console.log('修改成功')
              } else {
                this.isOld = true
                setInterval(
                  () => { this.isOld = false }, 2000)
              }
            })
        }
      }
    }
  },
  components: {
    useCode
  },
  beforeCreate () {
    this.$parent.head.title = '修改密码'
  }
}
</script>

<style lang="scss" scoped>
.body{
  background: $base-bg;
  @include center($head-h,0);
  padding-top: .1rem;
  .real{
    margin: .3rem .15rem;
    width: calc(100vw - .15rem - .15rem);
    line-height: .44rem;
    color: #fff;
    background: $btn-bg;
    font-size: $enter-f
  }
}
</style>



