<template>
    <div class="contract-borrow" v-if="contractShow.isBorrow">
        <p class="tc title">出借咨询与服务协议</p>
        <p>本协议由以下双方于{{today|dateText}}在中华人民共和国上海市签署：</p>
        <p>甲方（出借人）：
            <span class="tdl">{{contract.userName}}</span>
        </p>
        <p>证件类型：
            <span class="tdl">身份证</span>
        </p>
        <p>证件号：
            <span class="tdl">{{contract.userIdCard}}</span>
        </p>
        <p>乙方：上海夸客金融信息服务有限公司</p>
        <p>联系地址：上海市黄浦区斜土路 359 号 5 号楼</p>
        <br>
        <p>鉴于：</p>
        <p>乙方是一家在中国上海市合法成立并有效存续的有限责任公司，拥有 “夸客财富” 微信公众号以及 “夸客优富” APP（以下统称为 “本平台” ）的经营权，向本平台注册用户提供信用咨询及金融信息服务。</p>
        <br>
        <p>根据《中国人民共和国合同法》及相关法律法规的规定，甲、乙双方遵循平等、自愿、互利和诚信实用原则，经友好协商，就乙方为甲方提供借款人推荐及贷后管理服务、出借咨询服务达成一致，以兹信守。</p>
        <br>
        <p>第一条 释义</p>
        <p>在本协议中，除非上下文另有解释，下列用语具有一下含义：</p>
        <p>1.1 出借人：本协议甲方，指依照法律规定自主选择出借一定数量资金给他人或者以一定的数量资金让他人债权的，具有完全民事权利能力和民事行为能力的自然人。
        </p>
        <p>1.2 出借：将一定数量资金出借给他人或者以一定数量资金受让他人的债权的行为。
        </p>
        <p>1.3 借款人：指有合法用途的资金需求，经乙方筛选推荐给出借人并得到出借人一定的数量出借资金的自然人或企业。
        </p>
        <p>1.4 借款协议：指甲方授权乙方代其与借款人签署，约定由甲方将资金出借给特定借款人，由该特定借款人对出借人履行还款义务相关权利义务的协议。
        </p>
        <p>1.5 债权转让：指甲方将其相应《借款协议》项下权利义务全部或部分转让给乙方为其寻找的债权受让人。甲方将其债权转让受让人，相当以信件，邮件或者短信等书面形式通知借款人，甲方授权乙方代其发
        </p>
        <br>
        <p>该等签若方式与双方书面签营本协议具有相法律效力，双方同意受本合同条款和条件约束，双方均承 诺不得以电子签名为由主张本合间存在任何形式的翻症，甲方可登站乙方被信公众导或APP完本协议，签看双方亦可置去大大育网（www.fadada.com）直验所签看的合同。
        </p>
        <br>
        <p>附件一：授权委托书-出借确认和债权转让
        </p>
        <p>附件二：授权委托书-催收及讼</p>
        <br>
    </div>
</template>

<script>
export default{
  name: 'borrow-contract',
  props: {contractShow: Object, contract: Object, today: Number},
  data () {
    return {
    }
  },
  methods: {}
}
</script>

<style lang="scss" scoped>
</style>



