<template>
    <div class="contract-out" v-if="contractShow.isOut">
        <p class="tc title">授权委托书-出借确认和债权转让</p>
        <p>致：</p>
        <p>尊敬授权委托书-出借确认和债权转让</p>
        <br>
        <p>委托人：即本协议甲方</p>
        <p>受托人：软简银行（北京）投资管理有限公司</p>
        <br>
        <p>经双方友好协商一致，委托人委托受托人于以下授权范围内以委托人的名义 行事。在《出借咨询与服务协议》（编号<span v-if="underLine">____________</span>{{contract.contractCode}}）履行期间，受 托人带委托人办理下列事项。
        </p>
        <p>（一） 在借款人及其借款条件符合如下全部所列条件时，代为确认出借并以委 托人名义签署《借款协议》:
        </p>
        <p>1.借款人通过受托人的信用审核;</p>
        <p>2.借款人的借款年利率不低于15%，但不得超出法律保护的利率上限。</p>
        <p>二） 在受让其他出借人的债权时，所购买的目标债权中，借款人及借款条件 符合如下全部所列条件时，代为确认购买并以委托人的名义签署《债权转让协议》 ：
        </p>
        <p>1.借款人通过受托人的信用审核。</p>
        <p>2.借款人的借款年利率不低于15%，但不得超出法律保护的利率上限。</p>
        <p>（三） 为委托人寻找债权受让人，代委托人签署《债权转让协议》并代为发出 债权转让通知。
        </p>
        <p>四） 委托人对借款人、受让人身份及人数、借款金额、借款日期、出借日期 等不作特定限制和要求，授权受托人在代其确认出借时自行决定；委托人同意受 《借款协议》及/或《债权转让协议》条款和条件的约束，认可受托人在授权范 围内行事的一切法律行为和承担相应法律后果。
        </p>
        <p>（五） 委托人的债权协议（编号<span v-if="underLine">____________</span>{{contract.contractCode}}）详情，请致电客服400-80 8-7257。
        </p>
        <br>
        <p>授权期限：本《授权委托书》之授权期限与《出借咨询与服务协议》（编号<span v-if="underLine">____________</span>{{contract.contractCode}}）的有效期一致。受托人无权另授权。</p>
    </div>
</template>

<script>
export default{
  name: 'out-contract',
  props: {contractShow: Object, contract: Object, underLine: Boolean},
  data () {
    return {
    }
  },
  methods: {}
}
</script>

<style lang="scss" scoped>
</style>



