<template>
    <div class="continued-view bte8">
        <ul class="product-list container">
            <li class="mark" v-if="detail.mark">
                <span>{{detail.mark|mark}}</span>
                <img src="../../assets/money/markGold.png" alt="">
            </li>
            <li class="list-one">{{detail.productName}}</li>
            <li class="flex-bet list-two">
                <span>{{detail.interestRate*100|thousand}}</span>
                <span>{{(detail.endAt - detail.startAt)/(86400000)|deadLine}}</span>
                <span>{{detail.investmentAmount|thousand}}</span>
            </li>
            <li class="flex-bet list-three">
                <span>预期年化 (%)</span>
                <span>理财期限 ({{(detail.endAt - detail.startAt)/(86400000)|deadWord}})</span>
                <span>起投金额 (元)</span>
            </li>
        </ul>
        <div class="mid">
            <ul class="bbe8">
                <li class="flex-bet li-one">
                    <span class="line">起息日期</span>
                    <span>到期日期</span>
                </li>
                <li class="flex-bet li-two bbe8">
                    <span>{{detail.startAt|datePoint}}</span>
                    <span>{{detail.endAt|datePoint}}</span>
                </li>
                <li class="li-three tc">
                    <p class="one">{{detail.exceptEarnings|thousand}}</p>
                    <p class="two">预期收益
                        <span class="grey">（元）</span>
                    </p>
                </li>
            </ul>

        </div>
        <div class="back-money flex-bet container">
            <span>回款至</span>
            <p>{{detail.defaultCardBankName}}
                <span>(尾号{{detail.bankCard|lastNum}})</span>
            </p>
        </div>
        <p class="agree-line">
            <label for="choose" v-show="isChoose">
                <img src="../../assets/money/agree.png" alt="">
            </label>
            <label for="choose" v-show="!isChoose">
                <img src="../../assets/money/refuse.png" alt="">
            </label>
            <input type="checkbox" id="choose" v-model="isChoose">
            <span class="agree">本人已认真阅读并同意</span>
            <span class="blue" @click="checkContract()">《出借合同与服务协议》</span>
        </p>
        <p class="bottom-msg">
            <span class="msg-one">备注：</span>
            <span class="msg-two">续投会在投资到期时将投资本金自动转入下次投资中， 利息照常回款。
            </span>
        </p>
        <div class="bottom-money flex-bet container">
            <p>
                <span>投资本金</span>
                <span class="money-num">￥{{detail.money|thousand}}</span>
            </p>
            <button :disabled="!isChoose" :class="[{'btn-grey':!isChoose}, 'btn']" @click="goWrite()">确定</button>
        </div>
    </div>
</template>

<script>
export default{
  data () {
    return {
      id: '',
      isChoose: true,
      detail: {},
      back: () => { // 点击返回
        this.$router.back(-1)
      }
    }
  },
  created () {
    this.getProduct() // 获得产品数据
    this.$http(this.path.investDetail(this.id)).then(res => {
      this.detail = res.data.data
      console.log(res.data.code, this.detail)
    })
    this.$parent.head.title = '续投产品'
    this.$parent.head.isWhite = false
    this.$parent.head.isEnter = false
    this.$parent.head.isArrow = false
  },
  methods: {
    getProduct () { // 获得id
      this.id = this.$route.query.id
      console.log(this.id)
    },
    goWrite () { // 去合同签名
      this.$router.push({
        name: `contract`,
        query: {
          id: this.detail.contractId, // 合同id
          transcationId: this.id, // 交易id
          type: 2 // 显示出借协议
        }
      })
    },
    checkContract () { // 查看合同
      this.$router.push({ // 去合同
        name: `contract`,
        query: {
          type: 2
        }
      })
    }
  }
}
</script>

<style lang="scss" scoped>
.continued-view {
  @include center($head-h, $base-h);
  font-family: "PingFang-SC-Medium";
  background: $base-bg;
  .product-list {
    position: relative;
    margin: 0.1rem 0;
    background: #fff;
    .list-one {
      line-height: 0.5rem;
    }
    .list-two {
      line-height: 0.2rem;
      font-size: 0.15rem;
      & span:first-child {
        font-size: 0.24rem;
        color: $title-color;
      }
      & span:nth-child(1)::after {
        content: "";
        position: absolute;
        left: 33.3%;
        height: 0.35rem;
        width: 0.01rem;
        background: #e8e8e8;
      }
      & span:nth-child(2)::after {
        content: "";
        position: absolute;
        left: 66.66%;
        height: 0.35rem;
        width: 0.01rem;
        background: #e8e8e8;
      }
    }
    .list-three {
      line-height: 0.5rem;
      font-size: 0.13rem;
      color: $tip-color;
    }
  }
  .mark {
    height: 0;
    color: #fff;
    text-align: right;
    font-size: 0.12rem;
    span {
      position: absolute;
      top: 0;
      right: 0.24rem;
    }
  }
  .mid {
    display: flex;
    flex-direction: column;
    align-items: center;
    line-height: 0.3rem;
    background: #fff;
    .list-three {
      font-size: 0.12rem;
    }
    ul {
      padding-top: 0.2rem;
      padding-bottom: 0.1rem;
      width: 100%;
      .li-one {
        font-size: 0.15rem;
        color: $tip-color;
        .line::after {
          content: "";
          position: absolute;
          left: 49.9%;
          margin-top: 0.1rem;
          height: 0.35rem;
          width: 0.01rem;
          background: #e8e8e8;
          opacity: 0.62;
        }
      }
      .li-two {
        padding-bottom: 0.1rem;
        color: $list-color;
        font-size: 0.13rem;
      }
      .li-three {
        padding: 0.15rem 0;
        .one {
          font-size: 0.2rem;
          color: $title-color;
        }
        .two {
          font-size: 0.15rem;
          color: $tip-color;
        }
        .grey {
          color: $list-color;
        }
      }
      li {
        line-height: 0.3rem;
        span {
          flex-basis: 50%;
          text-align: center;
        }
      }
    }
  }
  .back-money {
    margin: 0.1rem 0;
    line-height: 0.55rem;
    background: #fff;
    p {
      color: $tip-color;
    }
    span {
      color: $list-color;
    }
  }
  .agree-line {
    padding: 0 0.2rem;
    input {
      display: none;
    }
    .agree {
      font-size: 0.12rem;
      color: $tip-color;
    }
    .blue {
      font-size: 0.12rem;
      color: $agree-color;
    }
  }
  .bottom-msg {
    margin-top: 0.85rem;
    padding: 0.05rem 0.2rem;
    font-size: 0.12rem;
    background: $base-bg;
    .msg-one {
      color: $tip-color;
    }
    .msg-two {
      color: $list-color;
    }
  }
  .bottom-money {
    position: fixed;
    bottom: 0;
    left: 0;
    right: 0;
    line-height: 0.6rem;
    font-size: 0.15rem;
    background: #fff;
    .money-num {
      color: #d7a863;
    }
    .btn {
      width: 1rem;
    }
  }
}
</style>



