<template>
<div >
  <img src="../assets/logo.png" class="logo" v-if="isLogo">
    <swiper :options="swiperOption">
      <swiper-slide>
        <img src="../assets/login/login1.png" class="banner">
      </swiper-slide>
       <swiper-slide>
        <img src="../assets/login/login2.png" class="banner">
      </swiper-slide>
       <swiper-slide>
        <img src="../assets/login/login3.png" class="banner">
        <button @click="gologin">立即注册</button>
      </swiper-slide>
    </swiper>
    <div class="swiper-pagination"></div>
</div>
</template>

<script>
import 'swiper/dist/css/swiper.css'
import { swiper, swiperSlide } from 'vue-awesome-swiper'
export default {
  name: 'login',
  data () {
    return {
      isLogo: true,
      swiperOption: { // swiper
        observer: true,
        observerParents: true,
        autoHeight: true,
        scrollbarHide: true,
        pagination: {
          el: '.swiper-pagination',
          type: 'bullets',
          clickable: true,
          bulletClass: 'circle',
          bulletActiveClass: 'circle-b'
        }
      }
    }
  },
  methods: {
    logo: function () {
      this.isLogo = false
    },
    gologin: function () {
      localStorage.setItem('old', 1)
      this.$router.push({name: 'code'})
    }
  },
  components: {
    swiper,
    swiperSlide
  },
  created () {
    // this.$router.push({name: 'main.recommend'})
  },
  mounted () {
    setInterval(() => {
      this.logo()
      if (localStorage.getItem('old') === 1) {
        this.$router.push({
          name: 'main.recommend'
        })
      }
    }, 1000)
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang='scss'>
.logo{
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  z-index: 2;
}
.banner{
  width: 100vw;
  height: 100vh;
}
.swiper-pagination{
  position: absolute;
  bottom: .6rem;
  left: 1.45rem;
  z-index: 1;
  width: .87rem;
  display: flex;
  justify-content: space-between;
}
button{
  position: absolute;
  bottom: 1rem;
  left: 50%;
  width: 2rem;
  font-size: $enter-f;
  line-height: .5rem;
  margin-left: -1rem;
  background: #617ec0;
  border-radius: 0.1rem;
  color:  #fff;
  text-align: center;
  z-index: 1;
}
</style>



