export default {
  my: { // 我的页面请求
    method: 'get',
    url: '/a/u/user/'
  },
  myMessage: function (id) { // 消息中心
    return {
      method: 'get',
      url: '/a/u/user/userMessage/list/' + id
    }
  },
  myHelp: { // 帮助中心
    method: 'get',
    url: '/a/u/user/help'
  },
  myOur: { // 关于我们
    method: 'get',
    url: '/a/u/user/about'
  },
  myIeda: function (id, data) { // 意见反馈
    return {
      method: 'post',
      url: '/a/u/user/feedback/' + id,
      params: data
    }
  },
  myUpdata: { // 系统升级
    method: 'get',
    url: '/a/u/user/Update'
  },
  myUse: function (id) {
    return { // 我都账户
      method: 'get',
      url: 'a/u/user/set/' + id
    }
  },
  myWater: { // 交易流水
    method: 'get',
    url: '/a/u/transaction-log/list'
  },
  myEmail: function (id, data) { // 修改电子邮箱
    return {
      method: 'put',
      url: '/a/u/user/set/' + id,
      params: data
    }
  },
  myAddres: function (id, data) { // 详细地址
    return {
      method: 'put',
      url: '/a/u/user/set/' + id,
      params: data
    }
  },
  myChangeCode: function (id, data) { // 修改密码
    return {
      method: 'put',
      url: 'a/u/user/pasd/' + id,
      params: data
    }
  },
  myBank: { // 请求银行卡
    method: 'get',
    url: '/a/u/Bank'
  },
  myAutoCard: function (id, idCard) { // 设置默认英航卡
    return {
      method: 'post',
      url: '/a/u/defultCard/' + id,
      params: idCard
    }
  },
  myRealCard: function (id, params) { // 身份证
    return {
      method: 'post',
      url: '/a/u/userImage/Upload/' + id,
      data: params,
      headers: {
        'Content-Type': 'multipart/form-data'
      }
    }
  },
  out: {
    method: 'get',
    url: '/a/u/user/logout'
  },
  myAddBankG: function (id) { // 添加银行卡的信息
    return {
      method: 'get',
      url: '/a/u/userBank/' + id
    }
  },
  myAddBank: { // 读取银行
    method: 'get',
    url: '/a/u/Bank'
  },
  myAddBankCard: function (id, params) { // 添加英航卡
    return {
      method: 'post',
      url: '/a/u/userBank/' + id,
      params: params
    }
  },
  note: function (id) { // 短信验证接口
    return {
      method: 'post',
      url: '/a/register/code',
      params: id
    }
  },
  code: function (param) { // 注册
    return {
      method: 'post',
      url: '/a/register',
      params: param
    }
  },
  reCode: function (param) { // 找回密码
    return {
      method: 'post',
      url: '/a/user/pswd',
      params: param
    }
  },
  banner: { // 推荐页
    method: 'get',
    url: '/a/content/banner'
  },
  invest: function (num) { // 我的投资
    return {
      method: 'get',
      url: '/a/u/transaction/list/' + num
    }
  },
  enter: function (params) {
    return {
      method: 'post',
      url: '/a/login',
      params: params
    }
  },
  product: { // 产品列表
    method: 'get',
    url: '/a/product/list'
  },
  // 续投列表
  continueList: {
    method: 'get',
    url: '/a/u/transaction/list/continue'
  },
  // 产品详情
  productDetail: function (id) {
    return {
      method: 'get',
      url: '/a/product/' + id
    }
  },
  // 输入投资金额，选卡
  inputMoney: {
    method: 'get',
    url: '/a/u/r/pay/investment'
  },
  // 投资详情
  investDetail: function (id) {
    return {
      method: 'get',
      url: '/a/u/transaction/' + id
    }
  },
  // 未签署合同
  contractNot: function (type) {
    return {
      method: 'get',
      url: '/a/u/contract/unsigned/' + type
    }
  },
  // 已署合同
  contractHave: function (id) {
    return {
      method: 'get',
      url: '/a/u/contract/' + id
    }
  },
  // 待签署合同
  contractWait: {
    method: 'get',
    url: '/a/u/contract/ready-signed'
  },
  // 签名
  Upwrite: function (data) {
    return {
      method: 'post',
      url: '/a/u/r/pay/uploadimg',
      data: data,
      headers: {
        'Content-Type': undefined
      }
    }
  },
  // 富有支付
  payMoney: function (params) {
    return {
      method: 'post',
      url: '/a/u/r/pay/contract',
      params: params
    }
  },
  // 支付成功，查看订单详情
  successPay: function (id) {
    return {
      method: 'get',
      url: '/a/u/r/pay/transaction/' + id
    }
  },
  // 添加续投订单
  continueContract: function (params) {
    return {
      method: 'post',
      url: '/a/u/r/pay/continue',
      params: params
    }
  }
}



