<script>
export default{
  data () {
    return {
      params: {},
      detail: {}
    }
  },
  created () {
    this.getProduct() // 获得产品数据
    this.$http(this.path.payMoney(this.params)).then(res => {
      console.log(res.data.code)
      if (res.data.code === 0) {
        this.detail = res.data
        console.log(this.detail)
      }
    })
  },
  methods: {
    OnLoadEvent () {
      document.forwardForm.submit()
    },
    getProduct () {
      var payMsg = JSON.parse(sessionStorage.getItem('payMsg'))
      this.params = payMsg
      console.log(this.params)
    }
  }
}
