<template>
    <div class="contract-litigation" v-if="contractShow.isLitigation">
        <p class="tc title">授权委托书-催收及诉讼</p>
        <p>委托人：即本协议甲方。</p>
        <p>受托人：软简银行（北京）投资管理有限公司</p>
        <br>
        <p>经双方友好协商一致，委托人委托受托人于以下授权范围内以委托人的名义行 事。就委托人为出借人的《借款协议》，当借款人违约时，受托人代委托人办理 下列事项：
        </p>
        <p>（一） 向借款人直接催收或转委托其他合作机构催收；</p>
        <p>（二） 接受催收回款，包括借款人应偿还的本金、利息和应支付的逾期违约金、 逾期罚款；
        </p>
        <p>（三） 在借款人违约时，授权受托人代本人提起诉讼、反诉；代为答辩；代为 承认、放弃、变更诉讼请求；代为调查收集征集；代为出庭参加诉讼；代为调节； 代为和解；代收法律文书；
        </p>
        <p>（四） 其他有利于委托人收回回款的行为，包括不限于处置抵押物（如有）、 处置债权等手段追索回款。
        </p>
        <br>
        <p>授权期限：</p>
        <p>本授权自《出借咨询与服务协议》签订之日起生效，至出借人《借款协议》 项下的全部债权获得清偿完毕之日止。
        </p>
    </div>
</template>

<script>
export default{
  name: 'litigation-contract',
  props: {contractShow: Object, contract: Object},
  data () {
    return {
    }
  },
  methods: {}
}
</script>

<style lang="scss" scoped>
</style>



