<template>
    <div class="month-view bte8">
        <ul class="top">
            <li class="one bbe8 bte8">
                <div class="one-first tc">
                    <span class="line">起息日期</span>
                    <span>到期日期</span>
                </div>
                <div class="one-second">
                    <span>{{params.dateAt|datePoint}}</span>
                    <span>{{params.endAt|datePoint}}</span>
                </div>
            </li>
            <li class="two container">
                <div class="two-num">
                    <p class="yellow tc">{{params.result|thousand}}</p>
                    <p class="tc">
                        <span class="two-text">预期收益</span>
                        <span class="grey">(元)</span>
                    </p>
                </div>
                <p class="two-tip grey">本计算器不考虑节假日延期，试算结果仅供参考
                </p>
            </li>
            <li class="three flex-bet container">
                <span>付息日期</span>
                <span>付息金额</span>
            </li>
        </ul>
        <ul class="mid-list">
            <li class="flex-bet bbe8 container" v-for="n in list" :key="n.index">
                <span class="grey">{{(params.dateAt) + day*30*n|date}}</span>
                <span class="mid-money">￥{{params.result/6|thousand}}</span>
            </li>
        </ul>
    </div>
</template>

<script>
export default{
  data () {
    return {
      params: {},
      day: 86400000,
      list: []
    }
  },
  created () {
    var params = JSON.parse(sessionStorage.getItem('key')) // 获得前面页面的数据
    console.log(params)
    this.params = params // 赋值
    this.$parent.head.title = this.params.name
    this.$parent.head.isWhite = false
    this.$parent.head.isEnter = false
    this.$parent.head.isArrow = false
    this.num()
  },
  computed: {
  },
  methods: {
    num () {
      for (let i = 1; i <= this.params.deadline / 30; i++) {
        this.list.push(i)
      }
    }
  }
}

</script>

<style lang="scss" scoped>
.month-view {
  @include center($head-h, 0);
  font-family: PingFang-SC-Medium;
  background: #f5f5f5;
  .top {
    position: fixed;
    top: 0.5rem;
    left: 0;
    right: 0;
    background: #fff;
    .one {
      padding: 0.24rem 0;
      line-height: 0.3rem;
      .one-first {
        position: relative;
        display: flex;
        justify-content: space-around;
        color: $tip-color;
        .line::after {
          content: "";
          position: absolute;
          left: 50%;
          top: 0.1rem;
          height: 0.385rem;
          width: 0.01rem;
          background: #d9d9d9;
        }
      }
      .one-second {
        display: flex;
        justify-content: space-around;
        color: $list-color;
      }
    }
    .two {
      line-height: 0.3rem;
      font-size: 0.16rem;
      .two-num {
        margin: 0.3rem 0;
      }
      .two-text {
        color: $tip-color;
      }
      .two-tip {
        font-size: 0.12rem;
      }
    }
    .three {
      line-height: 0.5rem;
      font-size: 0.15rem;
      background: #f5f5f5;
    }
  }
  .mid-list {
    margin-top: 3.07rem;
    line-height: 0.5rem;
    background: #fff;
    .mid-money {
      color: $title-color;
    }
  }
  .grey {
    color: $list-color;
  }
  .yellow {
    color: $title-color;
  }
}
</style>



