<template>
  <div class="body">
    <use-iphone :iphone='iphone'></use-iphone>
    <use-code :code='code'></use-code>
    <button class="btn" @click="go">登录</button>
    <div class="foot">
      <router-link :to="{name: 'code'}">立即注册</router-link>
      <a @click="wan">忘记密码?</a>
    </div>
    <transition name="fade">
      <p v-show="isOn" class="message">{{message}}</p>
    </transition>
  </div>
</template>

<script>
import useCode from '@/components/useCode'
import useIphone from '@/components/useIphone'
export default {
  data () {
    return {
      code: {
        title: '请输入登录密码',
        main: ''
      },
      iphone: {
        title: '请用手机号登录',
        main: ''
      },
      isOn: false,
      message: ''
    }
  },
  components: {
    useCode,
    useIphone
  },
  methods: {
    go: function () { // 登录请求
      let a = {
        phoneNumber: this.iphone.main,
        password: this.code.main
      }
      this.$http(this.path.enter(a))
        .then((rex) => {
          console.log(rex.data)
          if (rex.data.code === 0) {
            let b = this.iphone.main
            localStorage.setItem('id', b)
            console.log(localStorage.getItem('id'))
            this.message = rex.data.message
            this.isOn = true
            console.log(this.message)
            setInterval(() => {
              this.isOn = false
              this.$router.push({
                name: 'main.recommend'
              })
            }, 1000)
          }
          if (rex.data.code === -1) {
            this.message = rex.data.message
            this.isOn = true
            console.log(this.message)
            setInterval(() => {
              this.isOn = false
            }, 2000)
          }
        })
    },
    wan: function () {
      this.$router.push({name: 'code', query: {stdus: 'wan'}})
    }
  }
}
</script>

<style lang="scss" scoped>
.body{
    @include center($head-h,0);
    font-family: PingFang-SC-Medium;
    background: $base-bg;
    font-family: PingFang-SC-Medium;
    padding-top: .1rem;
    button{
      margin: .3rem .15rem 0 .15rem;
      width: calc(100vw - .15rem - .15rem);
      font-size: $enter-f;
      height: .44rem;
    }
    .foot{
    display: flex;
    justify-content: space-between;
    align-content: center;
    padding: .1rem .15rem 0 .15rem;
    color: #666;
    }
    .message{
      position: absolute;
      left: 50%;
      bottom: 1.68rem;
      line-height: $msg-h;
      width: $msg-w;
      margin-left: -$msg-w/2;
      font-size: $msg-f;
      color: #fff;
      background: $msg-bg;
      text-align: center;
    }
}
</style>



