<template>
    <div class="my-view">
    <header>
        <nav >
            <div class="logo">
                <i class="portrait"></i>
                <i class="v"></i>
                <span v-if="deg.realName">{{deg.realName}}</span>
                <span v-else>{{deg.phoneNumber}}</span>
            </div>
           <router-link :to="{name:'mySetting'}" class="fa fa-cog set"></router-link>
        </nav>
        <div class="asset">
            <p>总资产（元）</p>
            <p>{{deg.property}}</p>
        </div>
    </header>
    <main>
        <div class="earnings ">
            <p>累计收益</p>
            <p>{{deg.cumulativeIncome}}</p>
        </div>
        <ul class="list">
            <li v-for="item of list" :key='item.index'>
                <button @click="go(item.link)">
                    <i></i>
                    <span>{{item.name}}</span>
                    <i class="fa fa-angle-right"></i>
                </button>
            </li>
        </ul>
        <div class="note">
            <div>
                <i></i>
                <span>上班时间&emsp;</span>
                <span>9：00-18：00</span>
            </div>
            <div>
                <i></i>
                <span>客服电话&emsp;</span>
                <span>400-8000100</span>
            </div>
        </div>
    </main>
</div>
</template>

<script>
export default{
  data () {
    return {
      list: [{
        name: '银行卡管理',
        link: 'myBank'
      }, {
        name: '交易流水',
        link: 'myWater'
      }, {
        name: '消息中心',
        link: 'myMessage'
      }, {
        name: '账户设置',
        link: 'myUse'
      }],
      deg: {
        cumulativeIncome: 0.00, // 累计收益
        property: 0.00, // 总资产
        realName: '未实名', // 名字
        realStatus: '0' // 实名状态
      }
    }
  },
  methods: {
    go: function (url) {
      console.log(url)
      this.$router.push({name: url})
    }
  },
  created: function () {
    this.$parent.head.isHead = false // 影藏头部
    this.$parent.foot.isMy = true
    this.$http(this.path.my).then(rex => {
      console.log(rex.data)
      this.deg = rex.data.data
      let a = rex.data.data.id
      localStorage.setItem('id', a)
      console.log(localStorage.getItem('id'))
    })
  }
}
</script>

<style lang="scss" scoped>
  .my-view {
    @include center(0, $base-h);
    background: $base-bg;
    >header {
      height: 2.1rem;
      background: url(../../assets/my/my-bgc.png) no-repeat;
      background-size: 100%;
      font-family: PingFang-SC-Medium;
      nav{
        display: flex;
        align-items: center;
        justify-content: space-between;
        width: 100%;
        padding: 0 .15rem;
      }
      .logo {
        margin-top: .1rem;
        line-height: .36rem;
        .portrait {
          display: inline-block;
          width: .36rem;
          height: .36rem;
          background: url(../../assets/my/logo.png) no-repeat;
          background-size: 100%;
        }
        .v {
          display: inline-block;
          width: .16rem;
          height: .16rem;
          background: url(../../assets/my/v.png) no-repeat;
          background-size: 100%;
          transform: translate(-.2rem, .03rem)
        }
        span {
          font-size: $title-f;
          color: #fff;
          vertical-align: text-bottom;
        }
      }
      .set {
        width: .25rem;
        height: .25rem;
        font-size: .25rem;
        color: #fff;
      }
      .asset {
        margin-top: .73rem-.6rem;
        text-align: center;
        p {
          &:nth-child(1) {
            font-size: $sf;
            color: #fff;
          }
          &:nth-child(2) {
            margin-top: .2rem;
            font-size: .41rem;
            font-family: HelveticaNeue-Thin;
            color: #fff;
          }
        }
      }
    }
    main {
      .earnings {
        display: flex;
        justify-content: space-between;
        align-items: center;
        height: .5rem;
        margin-bottom: .05rem;
        padding: 0 .14rem;
        background: #fff;
        p {
          &:nth-child(1) {
            font-size: $mf;
            color: #464C5E;
          }
          &:nth-child(2) {
            font-size: $title-f;
            color: $content-color;
          }
        }
      }
      .list {
        li {
          padding: 0 .16rem;
          background: #fff;
          button {
            display: flex;
            align-items: center;
            height: .6rem;
            width: 100%;
            border-bottom: 1px solid #E8E8E8;
            text-align: left;
            position: relative;
            &:nth-child(4) {
              border: none;
            }
            span {
              font-family: PingFang-SC-Medium;
              font-size: $mf;
            }
            i {
              &:nth-child(1) {
                width: .23rem;
                height: .23rem;
                margin-right: .05rem;
              }
              &:nth-child(3) {
                position: absolute;
                right: 0;
                font-size: .2rem;
                color: #D4D4D4;
              }
            }
          }
          &:nth-child(1) {
            button {
              i {
                &:nth-child(1) {
                  background: url(../../assets/my/bank.png) no-repeat;
                  background-size: 100%;
                }
              }
            }
          }
          &:nth-child(2) {
            button {
              i {
                &:nth-child(1) {
                  background: url(../../assets/my/deal.png) no-repeat;
                  background-size: 100%;
                }
              }
            }
          }
          &:nth-child(3) {
            button {
              i {
                &:nth-child(1) {
                  background: url(../../assets/my/message.png) no-repeat;
                  background-size: 100%;
                }
              }
            }
          }
          &:nth-child(4) {
            button {
              i {
                &:nth-child(1) {
                  background: url(../../assets/my/accut.png) no-repeat;
                  background-size: 100%;
                }
              }
            }
          }
        }
      }
      .note {
        margin: 0.6rem auto 0 auto;
        width: 1.8rem;
        div {
          font-size: $tip-f;
          i {
            display: inline-block;
            width: .14rem;
            height: .14rem;
            vertical-align: bottom;
          }
          span {
            &:nth-child(2) {
              color: #666666;
            }
            &:nth-child(3) {
              color: #D6B87D;
            }
          }
          &:nth-child(1) {
            i {
              background: url(../../assets/my/shijian.png) no-repeat;
              background-size: 100%;
            }
          }
          &:nth-child(2) {
            i {
              background: url(../../assets/my/kefu.png) no-repeat;
              background-size: 100%;
            }
          }
        }

      }
    }
  }
</style>



