<template>
    <div class="inputMoney-view bte8">
        <ul class="tc">
            <li class="one">
                <img src="../../assets/money/productDetail/chooseDate.png" alt="">
                <span class="one-word">投资日期</span>
                <p @click="chooseDate()">
                    <span class="start-date bbe8">{{dateAt|datePoint}}</span>
                    <span class="fa fa-caret-down"></span>
                </p>
                <transition name="slide-fade">
                    <div class="alert flex-cent" @click.self="boxHide()" v-show="isDate">
                        <div class="border">
                            <Calendar ref="Calendar" v-on:choseDay="clickDay" v-on:isToday="clickToday" :agoDayHide='today'></Calendar>
                            <div class="btn-box tc bte8">
                                <button class="cancle" @click="boxHide()">取消</button>
                                <button class="confirm" @click="confirm()">确定</button>
                            </div>
                        </div>
                    </div>
                </transition>
            </li>
            <li class="two bbe8 bte8">
                <div class="two-first tc">
                    <span class="line">起息日期</span>
                    <span>到期日期</span>
                </div>
                <div class="two-second">
                    <span>{{(dateAt + this.detail.rateOfInterest * 86400000)|datePoint}}</span>
                    <span>{{endAt|datePoint}}</span>
                </div>
            </li>
            <li class="three">
                <p>
                    <img src="../../assets/money/productDetail/money.png" alt="">
                    <span>投资金额</span>
                    <span class="grey">（元）</span>
                </p>
                <input class="bbe8" v-model="money" @input="input()" type="text">
            </li>
            <li class="container">
                <button class="btn" @click="compute()">计算</button>
            </li>
        </ul>
        <p class="inputMoney-tip container">
            <span class="tip-one">备注：</span>
            <span class="tip-two">计算结果仅供参考</span>
        </p>
        <div class="result flex-bet container">
            <p class="result-one">预期收益
                <span>（元）</span>
            </p>
            <p class="result-two">{{result|thousand}}</p>
        </div>
        <transition name="slide-fade">
            <div class="msg-box tc" v-show="isInteger">
                <span class="msg">请输入10000的整数倍</span>
            </div>
        </transition>
    </div>
</template>

<script>
import Calendar from 'vue-calendar-component'
export default{
  data () {
    return {
      id: '',
      detail: {},
      money: '', // 输入金额
      isDate: false, // 日历隐藏
      isInteger: false, // 整数提示
      startDate: '', // 开始日期
      today: String((new Date().getTime()) - 86400000).substr(0, 10), // 前面不可选
      confirm: undefined,
      result: '', // 计算结果
      params: {}
    }
  },
  created () {
    this.getProduct()
    this.$http(this.path.productDetail(this.id)).then(res => {
      this.detail = res.data.data
      console.log(res.data.code, this.detail)
      this.$parent.head.title = this.detail.productName
      this.money = this.detail.investmentAmount
    })
    this.$parent.head.isWhite = false
    this.$parent.head.isEnter = false
    this.$parent.head.isArrow = false
    console.log(this.startDate)
  },
  mounted () {
  },
  computed: {
    dateAt: function () { // 起息日期
      if (this.startDate === '') { // 默认当天
        return (new Date().getTime())
      } else { // 转化为时间戳
        return new Date(this.startDate).getTime()
      }
    },
    endAt: function () { // 到期日期
      return Number((this.detail.deadline + this.detail.rateOfInterest) * 86400000 + this.dateAt)
    }
  },
  methods: {
    boxHide () { // 弹窗消失
      this.isDate = false
    },
    chooseDate () {
      this.isDate = true // 弹窗
    },
    clickDay (data) { // 选中某天
      console.log(data)
      this.confirm = function () {
        this.startDate = data
        this.isDate = false // 弹窗消失
      }
    },
    clickToday (data) { // 本月当天
      console.log(data)
      this.confirm = function () {
        this.startDate = data
        this.isDate = false
      }
    },
    getProduct () { // 获得id
      this.id = this.$route.query.id
      console.log(this.id)
    },
    input () { // 输入整数
      this.money = this.money.replace(/\D/g, '')
      console.log(this.money)
    },
    compute () { // 计算
      console.log(this.money % 10000, this.detail.refundStyle)
      if (this.money % 10000 !== 0 | this.money === '') { // 判断10000整数倍
        this.isInteger = true // 弹框
      } else { // 输出结果
        this.isInteger = false // 弹框消失
        this.result = this.money * this.detail.interestRate / 360 * this.detail.deadline // 显示结果
        if (this.detail.refundStyle === 0) { // 如果是按月付息产品跳转月月升
          var params = {
            name: this.detail.productName,
            dateAt: this.dateAt + this.detail.rateOfInterest * 86400000, // 起息日期
            endAt: this.endAt, // 到期
            result: this.result, // 利息结果
            deadline: this.detail.deadline // 期限
          }
          sessionStorage.setItem('key', JSON.stringify(params))
          this.$router.push({
            name: `monthResult`
          })
        }
      }
    }
  },
  components: {
    Calendar
  }
}

</script>

<style lang="scss" scoped>
.inputMoney-view {
  @include center($head-h, 0);
  font-family: PingFang-SC-Medium;
  background: $base-bg;
  ul {
    padding-bottom: 0.2rem;
    background: #fff;
    .one {
      display: flex;
      align-items: center;
      justify-content: center;
      line-height: 0.6rem;
      span {
        color: $title-color;
        font-size: 0.16rem;
      }
      .start-date {
        position: relative;
        margin-left: 0.1rem;
        width: 0.8rem;
        line-height: 0.2rem;
        color: $tip-color;
      }
      .fa-caret-down {
        color: #c3c3c3;
      }
    }
    .two {
      padding: 0.15rem 0;
      width: 100%;
      line-height: 0.3rem;
      .two-first {
        position: relative;
        display: flex;
        justify-content: space-around;
        color: $tip-color;
        .line::after {
          content: "";
          position: absolute;
          left: 50%;
          top: 0.1rem;
          height: 0.385rem;
          width: 0.01rem;
          background: #d9d9d9;
        }
      }
      .two-second {
        display: flex;
        justify-content: space-around;
        color: $list-color;
      }
    }
    .three {
      padding: 0.3rem 0;
      line-height: 0.35rem;
      color: $title-color;
      p {
        display: flex;
        align-items: center;
        justify-content: center;
        font-size: 0.16rem;
        .grey {
          color: $list-color;
        }
      }
      input {
        width: 0.9rem;
        text-align: center;
        color: $title-color;
        font-size: 0.18rem;
      }
      input::-webkit-input-placeholder {
        color: $title-color;
      }
    }
    .btn {
      width: 100%;
      color: #fff;
    }
    img {
      margin-right: 0.05rem;
    }
  }
  .inputMoney-tip {
    line-height: 0.4rem;
    font-size: 0.12rem;
    .tip-one {
      color: $tip-color;
    }
    .tip-two {
      color: $list-color;
    }
  }
  .result {
    line-height: 0.6rem;
    background: #fff;
    .result-one {
      font-size: 0.15rem;
      span {
        color: $list-color;
      }
    }
    .result-two {
      font-size: 0.2rem;
      color: #d9a95d;
    }
  }
  .msg-box {
    margin-top: 0.3rem;
  }
  .flex-cent {
    flex-direction: column;
    .border {
      overflow: hidden;
      border-radius: 10px;
      .wh_container {
        margin: 0;
        max-width: 290px;
      }
      .btn-box {
        background: #e6b761;
        .confirm,
        .cancle {
          width: 49%;
          color: #fff;
          font-size: 0.16rem;
        }
      }
    }
  }
}
</style>



