<template>
    <div class="product-detail-view">
        <div class="top">
            <span class="top-title">{{detail.productName}}</span>
            <i class="fa fa-arrow-left" @click="back()"></i>
            <span class="one">预期年化收益率</span>
            <span class="two">{{detail.interestRate*100|thousand}}</span>
            <ul>
                <li class="flex-bet li-one">
                    <span class="line">理财期限({{detail.deadline|deadWord}})</span>
                    <span>起投金额（元）</span>
                </li>
                <li class="flex-bet li-two">
                    <span>{{detail.deadline|deadLine}}</span>
                    <span>{{detail.investmentAmount|thousand}}</span>
                </li>
            </ul>
        </div>
        <ul class="main">
            <li class="bbe8">
                <span>产品简介</span>
                <button :disabled="isMore" @click.prevent="goMore()">
                    <span class="main-more">查看更多</span>
                    <i class="fa fa-chevron-right"></i>
                </button>
            </li>
            <li>
                <div>
                    <img src="../../assets/money/productDetail/refundStyle.png" alt="">
                    <span class="main-left">还款方式</span>
                </div>
                <span class="main-right">{{detail.refundStyle|refundStyle}}</span>
            </li>
            <li>
                <div>
                    <img src="../../assets/money/productDetail/startDate.png" alt="">
                    <span class="main-left">起息日期</span>
                </div>
                <span class="main-right">{{detail.rateOfInterest|rateOfInterest}}自然日</span>
            </li>
            <li>
                <div>
                    <img src="../../assets/money/productDetail/backDate.png" alt="">
                    <span class="main-left">到期日期</span>
                </div>
                <span class="main-right">{{end()|date}}</span>
            </li>
            <li>
                <div>
                    <img src="../../assets/money/productDetail/productMsg.png" alt="">
                    <span class="main-left">产品备注</span>
                </div>
                <span class="main-right">自动还款，3个工作日内到账</span>
            </li>
            <li>
                <div>
                    <img src="../../assets/money/productDetail/moreMessage.png" alt="">
                    <span class="main-left">产品合同</span>
                </div>
                <div class="main-bottom" @click="checkContract()">
                    <span>查看合同</span>
                    <i class="fa fa-chevron-right"></i>
                </div>
            </li>
        </ul>
        <transition name='slide-fade'>
            <span class="msg" v-if="isMsg">请先实名绑卡</span>
        </transition>
        <div class="buttom-btn flex-bet container">
            <router-link :to="{name:'compute',query:{id:id}}"><img src="../../assets/money/productDetail/compute.png" alt="计算器"></router-link>
            <button class="btn" @click="goInvest()">立即投资</button>
        </div>
        <alert-box :alert='isContract' :detail='detail'></alert-box>
    </div>
</template>

<script>
import alert from './alert'
export default{
  data () {
    return {
      id: '',
      detail: {},
      isMsg: false,
      back: () => { // 点击返回
        this.$router.back(-1)
      },
      goContract: undefined,
      isContract: false,
      isMore: true
    }
  },
  created () {
    this.getProduct() // 获得产品数据
    this.$http(this.path.productDetail(this.id)).then(res => {
      console.log(res.data.code)
      if (res.data.code === 0) {
        this.detail = res.data.data
        if (this.detail.moreMessage) { // 是否有图
          this.isMore = false
        } else {
          this.isMore = true
        }
      } else if (res.data.code === 10010) { // 去实名
        this.$router.push({name: `myReal`})
      } else if (res.data.code === 10020) { // 去绑卡
        this.isMsg = true // 提示
        setTimeout(
          () => { this.$router.push({name: `myAddBank`}) }, 1000)
      }
      console.log(this.detail)
    })
  },
  methods: {
    goInvest () { // 去支付页面
      this.$router.push({
        name: `inputMoney`,
        query: {
          id: this.id // 产品id
        }
      })
    },
    getProduct () { // 获得id
      this.id = this.$route.query.id
      console.log(this.id, '产品id')
    },
    end () { // 结束时间计算
      return (new Date().getTime() + (this.detail.deadline + this.detail.rateOfInterest) * 86400000)
    },
    checkContract () { // 点击查看合同弹窗
      this.isContract = true
    },
    goMore () { // 查看更多
      this.$router.push({
        name: `more`,
        params: {
          msg: this.detail.moreMessage
        }
      })
    }
  },
  components: {
    'alert-box': alert
  }
}
</script>

<style lang="scss" scoped>
.product-detail-view {
  @include center(0, 0);
  font-family: "PingFang-SC-Medium";
  background: $base-bg;
  .top {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding-bottom: 0.2rem;
    color: #fff;
    background: linear-gradient(#ffb12e, #d6a956);
    .top-title {
      margin-top: 0.15rem;
      font-size: $head-f;
    }
    .fa-arrow-left {
      position: absolute;
      left: 0.2rem;
      top: 0.05rem;
      line-height: 0.4rem;
      width: 0.4rem;
      -webkit-text-stroke: 0.01rem #d6a956;
      font-size: $enter-f;
    }
    .one {
      font-size: 0.13rem;
      line-height: 0.4rem;
    }
    .two {
      font-size: 0.5rem;
      font-family: HelveticaNeue-Thin;
      line-height: 1rem;
      &::after {
        content: "%";
        font-size: 0.2rem;
      }
    }
    ul {
      width: 100%;
      .li-one {
        color: #f9e1b9;
        font-size: 0.12rem;
        .line::after {
          content: "";
          position: absolute;
          left: 50%;
          margin-top: 0.15rem;
          height: 0.35rem;
          width: 0.01rem;
          background: #fff;
          opacity: 0.62;
        }
      }
      .li-two {
        font-size: 0.17rem;
      }
      li {
        line-height: 0.3rem;
        span {
          flex-basis: 50%;
          text-align: center;
        }
      }
    }
  }
  .main {
    margin-top: 0.1rem;
    margin-bottom: 0.8rem;
    background: #fff;
    li {
      display: flex;
      justify-content: space-between;
      align-items: center;
      padding: 0 0.2rem;
      line-height: 0.4rem;
      img {
        margin-right: 0.1rem;
      }
      .fa-chevron-right {
        color: $tip-color;
        font-size: 0.15rem;
        vertical-align: middle;
        -webkit-text-stroke: 0.01rem #fff;
      }
      .main-more {
        font-size: 0.12rem;
        color: $tip-color;
      }
      .main-left {
        color: $list-color;
      }
      .main-right {
        color: $tip-color;
      }
      .main-bottom {
        color: #dfa584;
        .fa-chevron-right {
          color: #dfa584;
        }
      }
    }
  }
  .msg {
    position: absolute;
    bottom: 0.8rem;
    left: 50%;
    transform: translateX(-50%);
  }
  .buttom-btn {
    margin-bottom: 0.2rem;
    .btn {
      margin-left: 0.05rem;
      width: 100%;
      color: #fff;
    }
  }
}
</style>



