<template>
  <div class="case">
    <img src="../assets/login/code.png" class="logo">
     <input type="password" class="text" :placeholder="code.title" v-model="code.main" v-show="isClose" @input="z">
    <input type="text" class="text" :placeholder="code.title" v-model="code.main" v-show="isOpen" @input='z'>
    <img src="../assets/login/esc.png" class="esc" v-show="code.main" @click="escModel">
    <i class="eye" :class="{close: isClose,open: isOpen}" v-show="code.main" @click="trans"></i>
  </div>
</template>

<script>
export default {
  props: {
    code: Object
  },
  data () {
    return {
      isOpen: false,
      isClose: true
    }
  },
  methods: {
    escModel: function () {
      this.isClose = true
      this.isOpen = false
      this.code.main = ''
    },
    trans: function () {
      if (this.isClose) {
        this.isClose = false
        this.isOpen = true
      } else {
        this.isOpen = false
        this.isClose = true
      }
    },
    z: function () {
      let a = /\W/g
      this.code.main = this.code.main.replace(a, '')
    }
  }
}
</script>

<style lang="scss" scoped>
.case{
  display: flex;
align-items: center;
  height: .6rem;
  font-family: PingFang-SC-Medium;
  border-bottom: 0.01rem solid $base-bg;
  background: #fff;
  .logo{
    height: .18rem;
    padding: 0 .15rem;
    border-right: .01rem solid $base-bg;
  }
  .text{
    flex: 1 0;
    margin-left: .1rem;
    font-size: $tip-f;
  }
  .esc{
    width: .15rem;
    height: .15rem;
    margin: .05rem;
  }
  .eye{
    display: block;
    width: .21rem;
    height: .13rem;
    margin: .05rem;
  }
  .close{
    background: url(../assets/login/close.png) no-repeat;
    background-size: 100%;
  }
  .open{
    background: url(../assets/login/open.png) no-repeat;
    background-size: 100%;
  }
}
</style>



