<template>
    <div class="success-view bte8">
        <div class="tc">
            <img src="../../assets/pay/success.png" alt="">
            <p class="text">支付成功</p>
        </div>
        <div class="btn-box container">
            <button class="btn" @click="checDetail()">查看订单</button>
            <button class="btn btn-two" @click="returnMoney()">返回列表</button>
        </div>
    </div>
</template>

<script>

export default{
  data () {
    return {
    }
  },
  created () {
    this.$parent.head.title = '支付结果'
    this.$parent.head.isWhite = false
    this.$parent.head.isEnter = false
    this.$parent.head.isArrow = false
  },
  methods: {
    returnMoney () { // 回到输入金额
      this.$router.push({
        name: 'main.money'
      })
    },
    checDetail () {
      this.$router.push({
        name: `investDetail`
      })
    }
  }
}

</script>

<style lang="scss" scoped>
.success-view {
  @include center($head-h, 0);
  font-family: PingFang-SC-Medium;
  background: $base-bg;
  border-top: 0.02rem solid #e4c471;
  .tc{
      margin-top: 1.2rem;
  }
  .text {
    line-height: 0.4rem;
    color: #5BBC11;
  }
  .btn-box{
      position: absolute;
      left: 0;
      right: 0;
      bottom: .3rem;
      .btn{
          width: 100%;
      }
      .btn-two{
          margin-top: .05rem;
          background: $base-bg;
          border: .01rem solid #D7A85E;
          color: #E4C471;
      }
  }
}
</style>



