<template>
    <div class="pop" v-show="pop.isOn" @click.self="escBo()">
        <div>
           <p class="pop-p" :class="{'pop-is':pop.title}" >{{pop.title}}</p>
           <div class="pop-b">
           <ol>
             <li v-for="item of pop.main" :key="item.index">{{item}}</li>
           </ol>
           </div>
           <div class="pop-f">
           <button @click="esc">取消</button>
           <button @click="callback">{{pop.doText}}</button>
         </div>
         </div>
       </div>
</template>

<script>
export default {
  props: {
    pop: Object
  },
  methods: {
    esc () {
      this.pop.isOn = false
    },
    escBo () {
      this.pop.isOn = false
    },
    callback () {
      this.pop.callback()
    }
  }
}
</script>

<style lang="scss" scoped>
  .pop{
      display: flex;
      justify-content: center;
      align-items: center;
      position: fixed;
      top: 0;
      bottom: 0;
      left: 0;
      right: 0;
      z-index: 1;
      color: $list-color;
     background: rgba($color: #000000, $alpha: 0.7);
     >div{
       width: 3rem;
       border-radius: .2rem;
       background: #fff;
       .pop-p{
         line-height: .52rem;
         font-size: $title-f;
         text-align: center;
         color: #000000;
       }
       .pop-is{
         border-bottom: .01rem solid #E8E8E8;
       }
       .pop-b{
         font-size:$tip-f;
         ol{
           padding: .2rem 0;
           border-radius: .2rem;
           li{
             text-align: center;
           }
         }
       }
       .pop-f{
         display: flex;
         button{
           width: 50%;
           height: .47rem;
           border-top: .01rem solid #E8E8E8;
           color: $list-color;
           &:nth-child(2){
             border-left: .01rem solid #E8E8E8;
             color: $news-color;
           }
         }
       }
     }
   }
</style>



