<template>
  <div class="body bte8">
      <div class="main">
        <div class="style">
          <p>姓名</p>
          <input type="text" v-model="name" placeholder='输入真实姓名' @input="idZ">
        </div>
        <div class="style">
          <p>身份证号</p>
          <input type="text" v-model="idCard" placeholder="输入身份证号" @input="idCardZ">
        </div>
      </div>
      <button class="id-button" :class="{nd: isOn}" @click="next">下一步</button>
        <p class="message" v-show="isOn">请填写身份信息</p>
        <transition name="fade">
        <p class="message" v-show="isIdCard">身份证不正确</p>
        </transition>
    </div>
</template>

<script>
export default {
  data () {
    return {
      // isOn: false,
      isOne: true,
      name: '',
      idCard: '',
      isIdCard: false,
      isTwo: true
    }
  },
  methods: {
    idCardZ: function () { // 身份证不可以输入中文
      let a = /\W/g
      this.idCard = this.idCard.replace(a, '')
    },
    idZ: function () { // 姓名不可以数字下划线
      let a = /\w/g
      this.name = this.name.replace(a, '')
    },
    jcIdCard: function () { // 身份证格式检测
      let a18 = /^[1-9]\d{5}(18|19|([23]\d))\d{2}((0[1-9])|(10|11|12))(([0-2][1-9])|10|20|30|31)\d{3}[0-9Xx]$/
      let b15 = /^[1-9]\d{5}\d{2}((0[1-9])|(10|11|12))(([0-2][1-9])|10|20|30|31)\d{2}$/
      if (this.idCard.length === 15 || this.idCard.length === 18) {
        // 检测位数是否符合
        if (this.idCard.length === 15) { // 如果位数等于15
          return b15.test(this.idCard)
        } else { // 如果位数等于18
          return a18.test(this.idCard)
        }
      } else {
        return false
      }
    },
    next: function () { // 点击下一步执行的函数
      if (!this.isOn) {
        if (this.jcIdCard()) { // 验证格式是否通过
          this.$router.push({
            name: 'myRealCard',
            query: {
              id: this.name,
              idCard: this.idCard
            }
          })
        } else {
          this.isIdCard = true
          setInterval(
            () => { this.isIdCard = false }, 2000)
        }
      }
    }
  },
  computed: {
    isOn: function () {
      if (this.idCard && this.name) {
        return false
      } else {
        return true
      }
    }
  },
  beforeCreate () {
    this.$parent.head.title = '实名认证'
    if (this.$route.query) {
      console.log(this.$route.query)
      if (this.$route.query.stauds === '注册') {
        this.$parent.head.back = () => {
          this.$router.push({
            name: 'main.recommend'
          })
        }
        // 临时储存
        let b = this.$route.query.id
        localStorage.setItem('id', b)
        console.log(localStorage.getItem('id'))
      }
    }
  },
  mounted () {
    this.$parent.back = () => {
      this.$router.back(-1)
    }
  }
}
</script>

<style lang="scss" scoped>
.body{
  @include center($head-h,0);
  background: $base-bg;
  font-family: PingFang-SC-Medium;
  padding-top: .1rem;
  .main{
    background: #fff;
    padding-left: .15rem;
  }
  .style{
    display: flex;
    align-items: center;
    height: .6rem;
    border-bottom: .01rem solid $base-bg;
    p{
      width: .58rem + .15rem;
      padding-right: .15rem;
      margin-right: .15rem;
      border-right: 0.01rem solid $base-bg;
      text-align-last: justify;
    }
    input{
      font-size:$tip-f;
    }
  }
  .id-button{
    margin: .3rem .1rem 0 .1rem;
    width: calc(100vw - .1rem - .1rem);
    line-height: .44rem;
    background: $btn-bg;
    font-size: $enter-f;
    color: #fff;
  }
  .nd{
    background: #999;
  }
  .message{
    position: fixed;
    bottom: 1rem;
    left: 50%;
    margin-left: -$msg-w/2;
    width: $msg-w;
    line-height: $msg-h;
    text-align: center;
    background: $msg-bg;
    font-size: $msg-f;
    color: #fff;
  }
}
</style>



