<template>
   <div class="footText">
          <p>如需更换{{text}}信息请拨打客服电话</p>
          <div class="lastText">
            <img src="../../assets/my/kefu.png" >
            <span>客服电话</span>
            <span>400-800-0100</span>
          </div>
        </div>
</template>

<script>
export default {
  props: {
    text: {
      default: '身份'
    }
  }
}
</script>

<style lang="scss" scoped>
 .footText{
    display: inline-block;
      width: 2rem;
      font-size: $sf;
      color: #999999;
      text-align: center;
      .lastText{
        >img{
          width: .12rem;
        height: .12rem;
        }
        span{
          &:nth-child(3){
            color: #D6B87D
          }
        }
      }
    }
</style>



