<template>
  <div class="body">
    <hand :value='value' :draw='draw'></hand>
  </div>
</template>

<script>
// import vCanvas from './headLock'
import hand from '../hand'
export default {
  data () {
    return {
      value: true,
      draw: true
    }
  },
  components: {
    hand
  },
  beforeCreate () {
    console.log(this.$route.params)
    if (this.$route.params.id === 'change') {
      this.$parent.head.title = '修改手势密码'
    }
  }
}
</script>

<style lang="scss" scoped>
.body{
  @include center($head-h,0);
  display: flex;
  justify-content: center;
  background:#fff;
  border-top: $base-bg .01rem solid
}
</style>



