<template>
    <div>
        <header class="head-view container head-style bgwh" :class="{'head-orange':head.isWhite}">
            <i class="fa fa-arrow-left tl" :class="{'arrow-wh':head.isWhite,'vh':head.isArrow}" @click="head.back()"></i>
            <p class="head-title" :class="{'enter-title':head.isEnter}">{{head.title}}</p>
        </header>
        <router-view></router-view>
    </div>
</template>

<script>
export default {
  data () {
    return {
      head: {
        isWhite: false, // 箭头和标题颜色
        isEnter: false, // 标题字体
        title: '登录', // 标题内容
        isArrow: false, // 箭头显隐
        back: () => { // 点击返回
          this.$router.back(-1)
        }
      }
    }
  },
  methods: {
  }
}
</script>

<style lang='scss' scoped>
.head-view {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  line-height: $head-h;
  font-family: "PingFang-SC-Medium";
  text-align: center;
  z-index: 1;
  .fa-arrow-left {
    -webkit-text-stroke: 0.01rem #fff;
    float: left;
    width: 0.3rem;
    line-height: $head-h;
    font-size: $enter-f;
  }
  .head-title {
    font-size: $head-f;
    text-indent: -0.2rem;
  }
  .enter-title {
    font-size: $enter-f;
  }
  .arrow-wh {
    color: #fff;
    -webkit-text-stroke: 0.01rem $title-color;
  }
}

.head-style {
  //顶部栏高度50px,字体橘色
  height: $head-h;
  color: $title-color;
}

.head-orange {
  //头部背景变橘色，字体变白
  background: $title-color;
  color: #fff;
}
</style>



