<template>
    <div class="body">
      <ul>
        <li>
          <p>姓名</p>
          <p>{{realName.realName}}</p>
        </li>
        <li>
          <p>身份证号</p>
          <p>{{realName.idCard|id18}}</p>
        </li>
        <li>
          <p>城市</p>
          <div @click="go('myCtiy')">
            <p>{{params.ctiy}}</p>
            <i class="fa fa-angle-right"></i>
          </div>
        </li>
        <li>
          <p>开户行</p>
          <div>
          <p>{{params.bank}}</p>
          <i class="fa fa-angle-right"></i>
          </div>
        </li>
        <li>
          <p>银行卡号</p>
          <input type="text" @input="idCardZ" v-model="params.idCard" placeholder="选择持卡人银行账户">
        </li>
        <li>
          <p>预留手机</p>
          <input type="text" @input="iphoneZ" v-model="params.iphone" placeholder="输入银行预留手机号">
        </li>
      </ul>
    </div>
</template>

<script>
// import BScroll from 'better-scroll'
export default{
  data () {
    return {
      realName: {},
      params: {
        idCard: '',
        iphone: '',
        ctiy: '选择城市',
        bank: '选择开户行'
      }
    }
  },
  methods: {
    idCardZ: function () {
      let a = /\D/g
      this.params.idCard = this.params.idCard.replace(a, '')
    },
    iphoneZ: function () {
      let a = /\D/g
      this.params.iphone = this.params.iphone.replace(a, '')
    },
    go: function (url) {
      let a = this.params
      this.$router.push({
        name: url,
        query: {a}
      })
    }
  },
  beforeCreate () {
    this.$parent.head.title = '添加银行卡'
  },
  created () {
    let a = localStorage.getItem('id')
    this.$http(this.path.myAddBankG(a)) // 读取添加银行的默认信息
      .then((rex) => {
        console.log(rex.data)
        this.realName = rex.data.data
      })
    // this.$http(this.path.myAddBank) // 读取银行卡
    //   .then((rex) => {
    //     console.log(rex)
    //   })
  }
}
</script>

<style lang="scss" scoped>
.body{
    @include center($head-h,0);
    font-family: PingFang-SC-Medium;
    background: $base-bg;
    font-family: PingFang-SC-Medium;
    ul{
      margin-top: 0.1rem;
      padding: 0 .15rem;
       background: #fff;
      li{
        display: flex;;
        justify-content: space-between;
        align-items: center;
        height: .6rem;
        border-bottom: 0.01rem solid #E8E8E8;
        >p{
          &:nth-child(1){
          width: .7rem + .15rem;
          padding-right: .15rem;
          line-height: .14rem;
          border-right: .01rem solid #E8E8E8;
          font-size: $mf;
          color: #333333;
          text-align-last: justify;
          }
        }
        >div,>p:nth-child(2){
          flex: 1 0;
          padding-left: .15rem;
          line-height: .6rem;
          font-size: $tip-f;
          color: #999999;
        }
        >div{
          display: flex;
          justify-content: space-between;
          align-items: center;
          >i{
            color:#D4D4D4;
            font-size: .2rem;
          }
        }
        >input{
          flex: 1 0;
          padding-left: .15rem;
          font-size: $tip-f;
          line-height: .6rem;
          color: #333;
        }
      }
    }
}
</style>



