<template>
    <div class="invest-detail-view">
        <div class="top">
            <span class="top-title">{{detail.productName}}</span>
            <i class="fa fa-arrow-left" @click="back()"></i>
            <span class="one">预期收益（元）</span>
            <span class="two">{{detail.exceptEarnings|thousand}}</span>
            <ul>
                <li class="flex-bet li-one">
                    <span class="line">年化收益率</span>
                    <span>投资本金（元）</span>
                </li>
                <li class="flex-bet li-two">
                    <span class="per">{{detail.interestRate*100|thousand}}</span>
                    <span>{{detail.money|thousand}}</span>
                </li>
                <li class="flex-bet time-word">
                    <span>起息：{{detail.startAt|date}}</span>
                    <span>到息：{{detail.endAt|date}}</span>
                </li>
            </ul>
        </div>
        <ul class="main">
            <li>
                <div>
                    <img src="../../assets/money/investDetail/contract.png" alt="">
                    <span class="main-left">产品合同</span>
                </div>
                <button @click="checkContract()">
                    <span class="main-yellow">查看合同</span>
                    <i class="fa fa-chevron-right"></i>
                </button>
            </li>
            <li>
                <div>
                    <img src="../../assets/money/investDetail/style.png" alt="">
                    <span class="main-left">还款方式</span>
                </div>
                <span class="main-right">{{detail.refundStyle|refundStyle}}</span>
            </li>
            <li>
                <div>
                    <img src="../../assets/money/investDetail/bank.png" alt="">
                    <span class="main-left">回款账户</span>
                </div>
                <div>
                    <span class="main-right">{{detail.defaultCardBankName}}</span>
                    <span class="main-left">(尾号{{detail.defaultCardId|lastNum}})</span>
                </div>
            </li>
            <li>
                <div>
                    <img src="../../assets/money/investDetail/money.png" alt="">
                    <span class="main-left">起投金额</span>
                </div>
                <span class="main-yellow">￥{{detail.investmentAmount|thousand}}</span>
            </li>
            <li>
                <div>
                    <img src="../../assets/money/investDetail/status.png" alt="">
                    <span class="main-left">投资状态</span>
                </div>
                <span class="main-right">{{detail.status|investStatus}}</span>
            </li>
            <li>
                <div>
                    <img src="../../assets/money/investDetail/not.png" alt="">
                    <span class="main-left">未分配收益</span>
                </div>
                <span class="main-yellow">￥{{detail.notReturn|thousand}}</span>
            </li>
            <li>
                <div>
                    <img src="../../assets/money/investDetail/have.png" alt="">
                    <span class="main-left">已分配收益</span>
                </div>
                <span class="main-yellow">￥{{detail.returned|thousand}}</span>
            </li>
        </ul>
        <transition name="slide-fade">
            <div class="tc msg-box"  v-show="isContinue">
                <span class="msg">暂不可续投</span>
            </div>
        </transition>
        <div class="buttom-btn flex-bet container" v-if="show">
            <button class="btn" @click="continued()">立即续投</button>
        </div>
        <alert-box :alert='isContract' :detail='detail'></alert-box>
    </div>
</template>

<script>
import alert from './alert'
export default{
  data () {
    return {
      id: '',
      detail: {},
      show: true, // 是否是续投进入，是显示续投按钮
      isContinue: false, // 能否续投
      title: undefined,
      goThisContract: undefined,
      back: () => { // 点击返回
        this.$router.back(-1)
      },
      isContract: false // 合同查看
    }
  },
  created () {
    this.getProduct() // 获得产品数据
    this.$http(this.path.investDetail(this.id)).then(res => {
      console.log(res.data.code)
      if (res.data.code === 0) {
        this.detail = res.data.data
      }
      console.log(this.detail)
    })
  },
  methods: {
    getProduct () { // 获得id
      this.id = this.$route.query.id
      if (this.$route.query.show) { // 若是续投进入
        this.show = true // 显示续投按钮
      } else { // 否则不现实
        this.show = false
      }
      console.log(this.id, this.show)
    },
    checkContract () { // 点击查看合同弹窗
      this.isContract = true
    },
    end () { // 结束时间计算
      return (new Date().getTime() + (this.detail.deadline + this.detail.rateOfInterest) * 86400000)
    },
    continued () { // 续投
      if (this.detail.renuwalStatus === 1 && this.detail.productStatus === 0) { // 可续投
        this.isContinue = false // 不提示
        this.$router.push({ // 去续投
          name: `continued`,
          query: {
            id: this.detail.id
          }
        })
      } else { // 提示不可续投
        this.isContinue = true
        setTimeout(
          () => { this.isContinue = false }, 4000)
      }
    }
  },
  components: {
    'alert-box': alert
  }
}
</script>

<style lang="scss" scoped>
.invest-detail-view {
  @include center(0, 0);
  font-family: "PingFang-SC-Medium";
  background: $base-bg;
  .top {
    display: flex;
    flex-direction: column;
    align-items: center;
    color: #fff;
    background: linear-gradient(#f7b344, #d1a95e);
    .top-title {
      margin-top: 0.15rem;
      font-size: $head-f;
    }
    .fa-arrow-left {
      position: absolute;
      left: 0.2rem;
      top: 0.05rem;
      line-height: 0.4rem;
      width: 0.4rem;
      -webkit-text-stroke: 0.01rem #d6a956;
      font-size: $enter-f;
    }
    .one {
      margin-top: 0.3rem;
      line-height: 0.3rem;
    }
    .two {
      font-size: 0.3rem;
      font-family: HelveticaNeue-Light;
      line-height: 0.4rem;
    }
    ul {
      width: 100%;
      .li-one {
        color: #f9e1b9;
        font-size: 0.12rem;
        .line::after {
          content: "";
          position: absolute;
          left: 50%;
          margin-top: 0.15rem;
          height: 0.35rem;
          width: 0.01rem;
          background: #fff;
          opacity: 0.62;
        }
      }
      .li-two {
        font-size: 0.15rem;
      }
      .time-word {
        margin-top: 0.1rem;
        font-size: 0.12rem;
        line-height: 0.3rem;
        background: #e4ae56;
      }
      .per::after {
        content: "%";
        font-size: 0.12rem;
      }
      li {
        line-height: 0.3rem;
        span {
          flex-basis: 50%;
          text-align: center;
        }
      }
    }
  }
  .main {
    margin-top: 0.1rem;
    margin-bottom: 0.6rem;
    padding-top: 0.1rem;
    background: #fff;
    li {
      display: flex;
      justify-content: space-between;
      align-items: center;
      padding: 0 0.2rem;
      line-height: 0.4rem;
      img {
        margin-right: 0.1rem;
      }
      .fa-chevron-right {
        color: #d9a95d;
        font-size: 0.15rem;
        vertical-align: middle;
        -webkit-text-stroke: 0.01rem #fff;
      }
      .main-yellow {
        color: #d9a95d;
      }
      .main-left {
        color: $list-color;
      }
      .main-right {
        color: $tip-color;
      }
      .main-bottom {
        color: #d9a95d;
        .fa-chevron-right {
          color: #d9a95d;
        }
      }
    }
  }
  .msg-box {
    position: absolute;
    bottom: .75rem;
    left: 0;
    right: 0;
  }
  .buttom-btn {
    .btn {
      width: 100%;
      color: #fff;
    }
  }
  .contract-box {
    width: 3rem;
    border-radius: 0.1rem;
    background: #fff;
    .alert-one {
      margin-bottom: 0.2rem;
      line-height: 0.5rem;
      font-size: 0.17rem;
    }
    .alert-two {
      padding-left: 0.5rem;
      color: #666;
      line-height: 0.3rem;
    }
    .alert-three {
      margin-top: 0.2rem;
      width: 100%;
      color: #fff;
      line-height: 0.44rem;
      background: $btn-bg;
      font-size: 0.17rem;
      border-radius: 0 0 0.1rem 0.1rem;
    }
    .fa-circle {
      margin-right: 0.1rem;
      font-size: 0.12rem;
      color: #c0c0c0;
    }
    .cantract-click {
      color: $title-color;
      text-decoration: underline;
    }
    .fa-click {
      color: $title-color;
    }
    .btn-grey {
      background: $btn-disabled;
      color: #333;
    }
  }
}
</style>



