<template>
    <transition name="slide-fade">
        <div class="alert flex-cent" v-if="alert" @click.self="boxHide()">
            <ul class="contract-box">
                <li class="alert-one tc bbe8">查看合同</li>
                <li class="alert-two" :class="{'cantract-click':contractList.isOne}" @click="change('isOne')">
                    <i class="fa fa-circle" :class="{'fa-click':contractList.isOne}"></i>
                    <span>《出借咨询与服务协议》</span>
                </li>
                <li class="alert-two" :class="{'cantract-click':contractList.isTwo}" @click="change('isTwo')">
                    <i class="fa fa-circle" :class="{'fa-click':contractList.isTwo}"></i>
                    <span>《授权委托书-出租确认和债...》</span>
                </li>
                <li class="alert-two" :class="{'cantract-click':contractList.isThree}" @click="change('isThree')">
                    <i class="fa fa-circle" :class="{'fa-click':contractList.isThree}"></i>
                    <span>《授权委托书-催收及诉讼》</span>
                </li>
                <li class="alert-two" :class="{'cantract-click':contractList.isFour}" @click="change('isFour')">
                    <i class="fa fa-circle" :class="{'fa-click':contractList.isFour}"></i>
                    <span>《出借本金确认书》</span>
                </li>
                <li>
                    <button class="alert-three tc" :class="{'btn-grey':isAbled}" :disabled="isAbled" @click="goThisContract()">确认</button>
                </li>
            </ul>
        </div>
    </transition>
</template>

<script>
export default{
  name: 'alert-box',
  props: {alert: Boolean, detail: Object},
  data () {
    return {
      type: '',
      isAbled: true,
      contractList: {
        isOne: false,
        isTwo: false,
        isThree: false,
        isFour: false
      }
    }
  },
  methods: {
    boxHide () { // 弹窗隐藏
      this.$parent.isContract = false
      this.isAbled = true
      for (let key in this.contractList) {
        this.contractList[key] = false
      }
    },
    change (value) { // 点击合同变色
      this.isAbled = false
      for (let key in this.contractList) {
        if (key === value) {
          this.contractList[key] = true
          switch (value) {
            case 'isOne':
              this.type = 2
              break
            case 'isTwo':
              this.type = 3
              break
            case 'isThree':
              this.type = 4
              break
            case 'isFour':
              this.type = 5
              break
          }
          this.goThisContract = function () {
            this.$router.push({
              name: `contract`,
              query: {
                type: this.type,
                id: this.detail.contractId,
                money: this.detail.money
              }
            })
          }
        } else {
          this.contractList[key] = false
        }
      }
    }
  }
}
</script>

<style lang="scss" scoped>
.contract-box {
  width: 3rem;
  border-radius: 0.1rem;
  background: #fff;
  .alert-one {
    margin-bottom: 0.2rem;
    line-height: 0.5rem;
    font-size: 0.17rem;
  }
  .alert-two {
    padding-left: 0.5rem;
    color: #666;
    line-height: 0.3rem;
  }
  .alert-three {
    margin-top: 0.2rem;
    width: 100%;
    color: #fff;
    line-height: 0.44rem;
    background: $btn-bg;
    font-size: 0.17rem;
    border-radius: 0 0 0.1rem 0.1rem;
  }
  .fa-circle {
    margin-right: 0.1rem;
    font-size: 0.12rem;
    color: #c0c0c0;
  }
  .cantract-click {
    color: $title-color;
    text-decoration: underline;
  }
  .fa-click {
    color: $title-color;
  }
  .btn-grey {
    background: $btn-disabled;
    color: #333;
  }
}
</style>



