<template>
    <div id="canvasBox" :style="getHorizontalStyle">
        <div class="greet flex-bet bte8">
            <button class="greet-btn" @click="cancle">取消</button>
            <button class="greet-btn" @click="clear">清除</button>
            <button class="greet-btn" @click="upload">确定</button>
        </div>
        <p class="tip">
            <img src="../../assets/pay/tip.png" alt=""> 温馨提示：请在以下白色区域内写签名
        </p>
        <div class="canvas-box">
            <canvas></canvas>
        </div>
    </div>
</template>

<script>
import Draw from '../../../static/js/draw.js'
export default {
  name: 'my-canvas',
  props: {writeShow: Boolean},
  data () {
    return {
      detail: undefined
    }
  },
  components: {
    Draw
  },
  mounted () {
    this.canvasBox = document.getElementById('canvasBox')
    this.initCanvas()
  },
  computed: {
    getHorizontalStyle () {
      const d = document
      const w = window.innerWidth || d.documentElement.clientWidth || d.body.clientWidth
      const h = window.innerHeight || d.documentElement.clientHeight || d.body.clientHeight
      let length = (h - w) / 2
      let width = w
      let height = h
      if (this.canvasBox) {
        this.canvasBox.removeChild(document.querySelector('canvas'))
        this.canvasBox.appendChild(document.createElement('canvas'))
      }
      return {
        transform: `rotate(${this.degree}deg) translate(${length}px,${length}px)`,
        width: `${width}px`,
        height: `${height}px`,
        transformOrigin: 'center center'
      }
    }
  },
  methods: {
    initCanvas () {
      const canvas = document.querySelector('canvas')
      this.draw = new Draw(canvas)
    },
    clear () { // 清除
      this.draw.clear()
    },
    cancle () { // 取消
      this.$parent.writeShow = false
    },
    upload () {
      const image = this.draw.getPNGImage()
      var file = this.dataURLtoBlob(image)
      console.log(file)
      var reader = new FileReader()
      reader.readAsDataURL(file)
      console.log(reader)
      var formdata = new FormData()
      formdata.append('file', file)
      this.$http(this.path.Upwrite(formdata)).then(res => { // 续投
        if (res.data.code === 0) {
          this.detail = res.data
          console.log(res.data.code, this.detail)
          this.$parent.writeShow = false // 弹窗消失
          this.$parent.isUserSign = false // 灰色签名消失
          this.$parent.contract.userSign = this.detail.signUrl // 显示签名
          this.$parent.payMsg.userSign = this.detail.signUrl
          console.log(this.$parent.payMsg)
          sessionStorage.setItem('payMsg', JSON.stringify(this.$parent.payMsg))
          if (this.detail.signUrl) {
            this.$parent.confirm = () => {
              this.$parent.isWrite = false // 不弹窗
              this.$router.push( // 去富有支付
                {
                  name: 'payMoney'
                }
              )
            }
          }
        }
      })
    },
    dataURLtoBlob (dataurl) {
      var arr = dataurl.split(',')
      var mime = arr[0].match(/:(.*?);/)[1]
      var bstr = atob(arr[1])
      var n = bstr.length
      var u8arr = new Uint8Array(n)
      while (n--) {
        u8arr[n] = bstr.charCodeAt(n)
      }
      return new Blob([u8arr], {
        type: mime
      })
    }
  }
}
</script>

<style lang="scss" scoped>
#canvasBox {
  position: fixed;
  top: 0.5rem;
  left: 0;
  right: 0;
  bottom: 0;
  padding: 0 0.2rem;
  width: 100% !important;
  height: 100% !important;
  background: #fff;
  z-index: 1;
}
.tip {
  line-height: 0.3rem;
  background: #fef0da;
  img {
    margin: 0 0.02rem 0.02rem 0.06rem;
  }
}
.greet {
  position: absolute;
  bottom: 0.5rem;
  left: 0;
  right: 0;
  padding: 0.1rem 0.2rem;
  user-select: none;
}
.greet-btn {
  width: 1rem;
  line-height: 0.45rem;
  text-align: center;
  background: #e0b677;
  font-size: 0.18rem;
  color: #fff;
  border-radius: 10px;
}
.greet select {
  font-size: 18px;
}
.canvas-box {
  position: absolute;
  top: 0.3rem;
  left: 0.2rem;
  right: 0.2rem;
  bottom: 1.19rem;
  cursor: crosshair;
  background: #fff;
  canvas {
    width: 100%;
    height: 100%;
  }
}
</style>



