<template>
   <div class="case">
        <img src="../assets/login/checking.png" class="logo">
        <input type="text" class="text" v-model="checking.main" placeholder="输入验证码" @input="checkingZ">
        <p v-if="isNote" @click="yNote">短信验证</p>
        <p v-else class="cuo">重获{{note}}s</p>
        <p v-if="isVoice" @click="yVoice">语言验证</p>
        <p v-else class="cuo">重获{{voice}}s</p>
    </div>
</template>

<script>
export default {
  props: {
    checking: Object
  },
  data () {
    return {
      isNote: true,
      isVoice: true,
      isOne: false,
      note: 0,
      voice: 0,
      time: null
    }
  },
  methods: {
    checkingZ: function () { // 验证码检测
      let a = /\D/
      this.checking.main = this.checking.main.replace(a, '')
    },
    yNote: function () {
      if (this.checking.iphone.length === 11) {
        const t = 60
        if (!this.isOne) { // 是够是唯一倒计时的
          if (!this.time) { // 判断是可以发送接口
            this.note = t
            this.isNote = false // 显示倒计时
            this.isOne = true // 变成是唯一的倒计时
            console.log(this.checking.iphone)
            let a = {
              phoneNumber: this.checking.iphone
            }
            console.log(a)
            this.$http(this.path.note(a))
              .then((rex) => {
                console.log(rex)
              })
            this.time = setInterval(() => {
              if (this.note > 0 && this.note <= 60) {
                this.note = this.note - 1
                console.log(this.note)
              } else {
                this.isNote = true // 显示点击按钮
                clearInterval(this.time) // 清除定时器
                this.time = null
                this.isOne = false
                this.checking.cuo++ // 计算报错
              }
            }, 1000)
          }
        }
      } else {
        this.checking.mesg = '手机号格式错误'
      }
    },
    yVoice: function () {
      if (this.checking.iphone.length === 11) {
        const t = 60
        if (!this.isOne) { // 是够是唯一倒计时的
          if (!this.time) { // 判断是可以发送接口
            this.voice = t
            this.isVoice = false // 显示倒计时
            this.isOne = true // 变成是唯一的倒计时
            this.time = setInterval(() => {
              if (this.voice > 0 && this.voice <= 60) {
                this.voice = this.voice - 1
                console.log(this.note)
              } else {
                this.isVoice = true // 显示点击按钮
                clearInterval(this.time) // 清除定时器
                this.time = null
                this.isOne = false
                this.checking.cuo++
              }
            }, 1000)
          }
        }
      } else {
        this.checking.mesg = '手机号格式错误'
      }
    }

  }
}
</script>

<style lang="scss" scoped>
.case{
      display: flex;
      align-items: center;
      justify-content: space-between;
      height: .6rem;
      padding-right: .15rem;
      font-family: PingFang-SC-Medium;
      border-bottom: 0.01rem solid $base-bg;
      background: #fff;
      p{
        color: #E4C471;
        font-size: $tip-f;
        width: .65rem;
        text-align: center;
      }
      .cuo{
        color: #999
      }
.logo{
  height: .18rem;
  padding: 0 .15rem;
  border-right: .01rem solid $base-bg;
}
}
</style>



