<template>
  <div class="body">
    <ul>
      <li>
        <use-iphone :iphone='iphone'  @isF='x'></use-iphone>
      </li>
      <li>
        <use-checking :checking='checking'></use-checking>
      </li>
      <li>
        <use-code :code='code1'></use-code>
      </li>
      <li>
        <use-code :code='code2'></use-code>
      </li>
      <li class="case" v-if='isS'>
        <img src="../assets/login/kefu.png" class="logo">
        <input type="text"  class="text" placeholder="输入理财经理工号（选填）" v-model="kefu" @input="kefuZ">
      </li>
    </ul>
    <div class="rule" v-if="isS">
      <p>注册表示同意</p>
      <router-link :to="{name: 'privacy'}">《用户协议》</router-link>
      <p>和</p>
      <router-link :to="{name: 'privacy'}">《隐私条款》</router-link>
    </div>
    <button class="btn" @click="go">{{isbuttom}}</button>
    <div class="enter" v-if="isS">
      <span>已有账号?</span>
      <router-link :to="{name: 'enter'}">登录</router-link>
    </div>
    <transition name="fade">
    <p class="message" v-show="isOn">{{message}}</p>
    </transition>
    </div>
</template>

<script>
import useCode from '@/components/useCode'
import useIphone from '@/components/useIphone'
import useChecking from '@/components/useChecking'
export default {
  data () {
    return {
      code1: {
        title: '输入6-16位字符和数字密码',
        main: ''
      },
      code2: {
        title: '再次输入确认密码',
        main: ''
      },
      iphone: {
        title: '输入11位手机号',
        main: ''
      },
      kefu: '',
      checking: {
        main: '',
        cuo: 0,
        iphone: '',
        mesg: ''
      },
      message: '',
      isOn: false,
      isS: true,
      isbuttom: '立即注册'
    }
  },
  components: {
    useCode,
    useIphone,
    useChecking
  },
  methods: {
    kefuZ: function () { // 客服经理正则检测
      let a = /\D/
      this.kefu = this.kefu.replace(a, '')
    },
    go: function () {
      if (this.code1.main && this.code2.main && this.iphone.main && this.checking.main) {
        // 判断是否都填写了
        if (this.iphone.main.length === 11) { // 手机格式是否正确
          if (this.code1.main.length < 17 && this.code1.main.length > 5) {
            // 检测密码是否有问题
            if (this.code1.main === this.code2.main) {
              let a = {
                phoneNumber: this.iphone.main,
                code: this.checking.main,
                password: this.code1.main
              }
              if (this.$route.query.stdus === 'wan') { // 修改密码
                this.$http(this.path.reCode(a))
                  .then((rex) => {
                    console.log(rex)
                    if (rex.data.code === 0) {
                      this.message = rex.data.message
                      this.isOn = true
                      let b = this.iphone.main
                      localStorage.setItem('id', b)
                      setInterval(() => {
                        this.isOn = false
                        this.$router.push({
                          name: 'myReal'
                        })
                      }, 2500)
                    } else if (rex.data.code === -1) {
                      this.message = rex.data.message
                      this.isOn = true
                      setInterval(() => {
                        this.isOn = false
                      }, 2500)
                    }
                  })
              } else { // 注册页面
                this.$http(this.path.code(a))
                  .then((rex) => {
                    console.log(rex)
                    if (rex.data.code === 0) {
                      this.message = rex.data.message
                      this.isOn = true
                      let b = this.iphone.main
                      localStorage.setItem('id', b)
                      setInterval(() => {
                        this.isOn = false
                        this.$router.push({
                          name: 'myReal'
                        })
                      }, 2500)
                    } else if (rex.data.code === -1) {
                      this.message = rex.data.message
                      this.isOn = true
                      setInterval(() => {
                        this.isOn = false
                      }, 2500)
                    }
                  })
              }
            } else { // 两此密码不一致
              this.message = '两次密码不一致'
              this.isOn = true
              setInterval(() => {
                this.isOn = false
              }, 2500)
            }
          } else { // 密码格式有问题
            this.message = '密码格式错误'
            this.isOn = true
            setInterval(() => {
              this.isOn = false
            }, 2500)
          }
        } else { // 手机号·格式有问题报错
          this.message = '手机号格式错误'
          this.isOn = true
          setInterval(() => {
            this.isOn = false
          }, 2500)
        }
      } else {
        switch (true) {
          case !this.iphone.main:
            this.message = '请填写手机号'
            break
          case !this.checking.main:
            this.message = '请填写验证码'
            break
          case !this.code1.main || !this.code2.main:
            this.message = '请填写密码'
            break
        }
        this.isOn = true
        setInterval(() => {
          this.isOn = false
        }, 2500)
      }
    },
    x: function (v) { // 往验证码里填写数据
      // console.log(v)
      this.checking.iphone = v
    }
  },
  comments: {
  },
  watch: {
    checking: {
      header (val, oldval) {
        console.log(val)
        // if (val.mesg === '手机号格式错误') {
        //   // this.message = val.mesg
        //   // this.isOn = true
        //   // val.mesg = ''
        //   console.log(val, oldval)
        // }
      }
    }
  },
  beforeCreate () {
    if (this.$route.query.stdus === 'wan') {
      this.$parent.head.title = '找回密码'
      this.$parent.head.back = () => {
        this.$router.back(-1)
      }
    } else {
      this.$parent.head.title = '注册' // 标题内容
      this.$parent.head.back = () => {
        this.$router.push({name: 'main.recommend'})
      }
    }
  },
  created () {
    if (this.$route.query.stdus === 'wan') {
      this.isS = false
      this.isbuttom = '确认'
    }
  },
  destroyed () { // 销毁前重置点击事件
    this.$parent.head.back = () => {
      this.$router.back(-1)
    }
  }
}
</script>

<style lang="scss" scoped>
.body{
    @include center($head-h,0);
    font-family: PingFang-SC-Medium;
    background: $base-bg;
    ul{
      margin-top:.1rem;
      .case{
      display: flex;
      align-items: center;
      height: .6rem;
      font-family: PingFang-SC-Medium;
      border-bottom: 0.01rem solid $base-bg;
      background: #fff;
       .logo{
          height: .18rem;
          padding: 0 .15rem;
          border-right: .01rem solid $base-bg;
        }
        .text{
          flex: 1 0;
          margin-left: .1rem;
          font-size: $tip-f;
        }
    }}
    .rule{
      display: flex;
      margin-left: .15rem;
      margin-top: .07rem;
      font-size: $sf;
      color: #666;
      a{
        color: #18ACFF;
      }
    }
    button{
      margin: .3rem .16rem .1rem .16rem;
      width: calc(100vw - .17rem - .17rem)
    }
    .enter{
      float: right;
      margin-right: .16rem;
      font-size: $tip-f;
      a{
        color:$content-color;
      }
    }
    .message{
      position: absolute;
      left: 50%;
      bottom: .5rem;
      line-height: $msg-h;
      width: $msg-w;
      margin-left: -$msg-w/2;
      font-size: $msg-f;
      color: #fff;
      background: $msg-bg;
      text-align: center;
    }
}
</style>



