import Vue from 'vue'
import Router from 'vue-router'
import login from '@/components/login'
import mainView from '@/components/mainView'
import my from '@/components/my/my'
import recommend from '@/components/recommend/recommend'
import money from '@/components/money/money'
import productDetail from '@/components/money/productDetail'
import inputMoney from '@/components/pay/inputMoney'
import success from '@/components/pay/success'
import failed from '@/components/pay/failed'
import compute from '@/components/money/compute'
import monthResult from '@/components/money/monthResult'
import more from '@/components/money/more'
import contract from '@/components/contract/contract'
import payMoney from '@/components/pay/payMoney'
import investDetail from '@/components/money/investDetail'
import continued from '@/components/money/continued'
import sonView from '@/components/sonView'
import myMessage from '@/components/my/myMessage'
import mySetting from '@/components/my/mySetting'
import myHelp from '@/components/my/myHelp'
import grayHead from '@/components/grayHead'
import myOur from '@/components/my/myOur'
import myIeda from '@/components/my/myIeda'
import myUse from '@/components/my/myUse'
import myEmail from '@/components/my/myEmail'
import myChangeCode from '@/components/my/myChangeCode'
import myHand from '@/components/my/myHand'
import myBank from '@/components/my/myBank'
import myReal from '@/components/my/myReal'
import myRealCard from '@/components/my/myRealCard'
import invest from '@/components/invest/invest'
import myAddBank from '@/components/my/myAddBank'
import myCtiy from '@/components/my/myCtiy'
import code from '@/components/code'
import privacy from '@/components/privacy'
import enter from '@/components/enter'
import myWater from '@/components/my/myWater'
import banner from '@/components/banner'
Vue.use(Router)

export default new Router({
  routes: [
    { // 软件加载页面+第一次使用的引导页面，如果不是第一次打开就跳往推荐页面
      path: '/',
      name: 'login',
      component: login
    },
    { // 头尾的模板指令（每个页面设定头尾的v-show,变量为true,或者false，来决定头尾模板的显示
      path: '/main',
      name: 'main',
      component: mainView,
      children: [
        { // 推荐页面默认首页
          path: '/recommend',
          name: 'main.recommend',
          component: recommend
        },
        { // 理财页面
          path: 'money',
          name: 'main.money',
          component: money
        },
        { // 投资页面
          path: 'invest',
          name: 'main.invest',
          component: invest
        },
        { // 我的页面
          path: 'my',
          name: 'main.my',
          component: my
        }
      ]
    },
    { // 只有头部的样式
      path: '/a',
      name: 'son',
      component: sonView,
      children: [
        { // 注册
          path: '/code',
          name: 'code',
          component: code
        },
        { // 登录
          path: '/enter',
          name: 'enter',
          component: enter
        },
        { // 用户协议
          path: '/privacy',
          name: 'privacy',
          component: privacy
        },
        { // 消息中心
          path: '/my/myMessage',
          name: 'myMessage',
          component: myMessage
        },
        { // 跟多设置
          path: '/my/mySetting',
          name: 'mySetting',
          component: mySetting
        },
        { // 交易流水页面
          path: '/my/water',
          name: 'myWater',
          component: myWater
        },
        { // 我的账户设置
          path: '/my/myUse',
          name: 'myUse',
          component: myUse
        },
        { // 修改邮箱和地址
          path: '/my/message/:id',
          name: 'myEmail',
          component: myEmail
        },
        { // 修改密码
          path: '/my/myCode',
          name: 'myCode',
          component: myChangeCode
        },
        { // 修改手势密码
          path: '/my/myHand/:id',
          name: 'myHand',
          component: myHand
        },
        { // 银行卡管理
          path: '/my/myBank',
          name: 'myBank',
          component: myBank
        },
        { // 实名认证
          path: '/my/myReal',
          name: 'myReal',
          component: myReal
        },
        { // 上传身份证
          path: '/my/myRealCard',
          name: 'myRealCard',
          component: myRealCard
        },
        { // 添加银行卡
          path: '/my/myAddBank',
          name: 'myAddBank',
          component: myAddBank
        },
        { // 添加城市
          path: '/my/myCtiy',
          name: 'myCtiy',
          component: myCtiy
        },
        { // 产品计算页面
          path: '/money/compute',
          name: 'compute',
          component: compute
        },
        { // 按月付息产品计算结果
          path: '/money/monthResult',
          name: 'monthResult',
          component: monthResult
        },
        {// 产品更多页面
          path: '/money/more',
          name: 'more',
          component: more
        },
        {// 合同详情
          path: '/contract',
          name: 'contract',
          component: contract
        },
        {// 产品续投
          path: '/money/continued',
          name: 'continued',
          component: continued
        },
        {// 产品投资
          path: '/pay/inputMoney',
          name: 'inputMoney',
          component: inputMoney
        },
        {// 支付成功
          path: '/pay/success',
          name: 'success',
          component: success
        },
        {// 支付失败
          path: '/pay/failed',
          name: 'failed',
          component: failed
        },
        { // 活动
          path: '/banner',
          name: 'banner',
          component: banner
        }
      ]
    },
    { // 头部是灰色的样式
      path: '/a',
      name: 'gray',
      component: grayHead,
      children: [
        {
          path: '/my/myHelp',
          name: 'myHelp',
          component: myHelp
        },
        {
          path: '/my/myOur',
          name: 'myOur',
          component: myOur
        },
        {
          path: '/my/myIeda',
          name: 'myIeda',
          component: myIeda
        }
      ]
    },
    { // 产品详情
      path: '/money/productDetail',
      name: 'productDetail',
      component: productDetail
    },
    { // 投资详情
      path: '/money/investDetail',
      name: 'investDetail',
      component: investDetail
    },
    { // 投资详情
      path: '/payMoney',
      name: 'payMoney',
      component: payMoney
    }
  ]
})



