<template>
  <div class="body ">
    <ul class="tag flex-bet tc bbe8 swiper-pagination bte8">
      <li class=" buttom-li " role="button" aria-label="Go to slide 1">理财中</li>
      <li class=" buttom-li " role="button" aria-label="Go to slide 2">退出中</li>
      <li class=" buttom-li " role="button" aria-label="Go to slide 3">已退出</li>
    </ul>
    {{isTranstion}}
    <swiper :options="swiperOption" >
      <swiper-slide data-hash="slide1">
        <ul class="container">
          <li v-for="item of investing" :key="item.index" class="investing" @click="go(item.id)">
            <div class="logo">
              <div>{{item.mark|mark}}</div>
            </div>
            <div class="main">
              <div class="left">
                <img src="../../assets/invest/newY.png" v-if="item.mark ===1">
                <img src="../../assets/invest/Y.png" v-else>
                <p class="staus">{{item.status|investStatus}}</p>
                <p class="product">{{item.productName}}</p>
              </div>
              <div class="center">
                <p class="staus-p grad">{{item.interestRate*100}}</p>
                <p class="product">预期年化（%）</p>
              </div>
              <div class="right">
                <p class="staus-p">{{item.money|wh}}</p>
                <p class="product">投资金额（元）</p>
              </div>
            </div>
            <div class="date"></div>
          </li>
        </ul>
      </swiper-slide>
      <swiper-slide data-hash="slide2">
          <ul class="container">
          <li v-for="item of outing" :key="item.index" class="investing"  @click="go(item.id)">
            <div class="logo" >
              <div v-if="item.mark ===1">{{item.mark|mark}}</div>
            </div>
            <div class="main">
              <div class="left">
                <img src="../../assets/invest/Y.png" >
                <p class="staus">{{item.status|investStatus}}</p>
                <p class="product">{{item.productName}}</p>
              </div>
              <div class="center">
                <p class="staus-p grad">{{item.interestRate*100}}</p>
                <p class="product">预期年化（%）</p>
              </div>
              <div class="right">
                <p class="staus-p">{{item.money|wh}}</p>
                <p class="product">投资金额（元）</p>
              </div>
            </div>
            <div class="date"></div>
          </li>
        </ul>
      </swiper-slide>
      <swiper-slide data-hash="slide3">
        <ul class="container">
          <li v-for="item of outing" :key="item.index" class="investing"  @click="go(item.id)">
            <div class="logo" >
              <div v-if="item.mark ===1">{{item.mark|mark}}</div>
            </div>
            <div class="main">
              <div class="left">
                <img src="../../assets/invest/Y.png" >
                <p class="staus">{{item.status|investStatus}}</p>
                <p class="product">{{item.productName}}</p>
              </div>
              <div class="center">
                <p class="staus-p grad">{{item.interestRate*100}}</p>
                <p class="product">预期年化（%）</p>
              </div>
              <div class="right">
                <p class="staus-p">{{item.money|wh}}</p>
                <p class="product">投资金额（元）</p>
              </div>
            </div>
            <div class="date"></div>
          </li>
        </ul>
      </swiper-slide>
    </swiper>
   <div class='flex-cent continue-box' v-if='isContinue'>
          <img src='../../assets/invest/noinvest.png' alt='立即投资'>
          <p class='tip'>由于网络连接失败，目前无法显示内容</p>
          <p class="tip">请点击下方按钮刷新页面</p>
          <a class='btn continue-invest' @click="refresh()">刷新</a>
    </div>
  </div>
</template>

<script>
import 'swiper/dist/css/swiper.css'
import { swiper, swiperSlide } from 'vue-awesome-swiper'
export default {
  data () {
    return {
      swiperOption: { // swiper
        observer: true,
        observerParents: true,
        // hashNavigation: true,
        pagination: {
          el: '.swiper-pagination',
          // type: 'bullets',
          // type: 'fraction',
          // type : 'progressbar',
          type: 'custom',
          clickable: true,
          bulletClass: 'buttom-li',
          bulletActiveClass: 'buttom-add',
          clickableClass: 'buttom-add'
        }
      },
      investing: {}, // 理财中对象存放
      outing: {}, // 回款中对象
      outOver: {} // 已退出
    }
  },
  watch: {
    isTranstion: function () {
      /* eslint-disable */
     console.log(this.isTranstion)
    }
  },
  computed: {
    isContinue: function () {
      if (this.investing === null || this.outing === null || this.outOver === null) {
        return true
      } else {
        return false
      }
    },
    isTranstion: function () {
      /* eslint-disable */
      return $('.swiper-wrapper').css('transform')
    }
  },
  created () {
    this.$parent.head.isHead = true // 头部显示
    this.$parent.head.title = '我的投资' // 理财列表
    this.$parent.head.isBell = true // 铃铛隐藏
    this.$parent.head.isWhite = false // 标题不变色
    this.$parent.head.isEnter = false // 标题字体不变
    this.$parent.foot.isInsvet = true // 底部按钮变色
    this.$http(this.path.invest(0)) // 请求理财中
      .then((rex) => {
        console.log(rex.data.data)
        this.investing = rex.data.data
      })
    this.$http(this.path.invest(1)) // 请求回款中
      .then((rex) => {
        console.log(rex.data.data)
        this.outing = rex.data.data
      })
    this.$http(this.path.invest(2)) // 请求已退出
      .then((rex) => {
        console.log(rex.data.data)
        this.outOver = rex.data.data
      })
  },
  methods: {
    refresh () {
      window.location.reload()
    },
    go: function (id) {
      console.log(id)
      this.$router.push({
        name: 'investDetail',
        query: {id: id}
      })
    }
  },
  components: {
    swiper,
    swiperSlide
  },
  filters: {
    wh: function (val) {
      if (val) {
        return val.replace(/(\d)(?=(?:\d{3})+$)/g, '$1,')
      }
    }
  }
}
</script>

<style lang='scss' scoped>
.body {
  @include center($head-h, 0);
  background: $base-bg;
  font-family: PingFang-SC-Medium;
  .tag {
    position: fixed;
    top: $head-h;
    left: 0;
    right: 0;
    background: #fff;
    z-index: 2;
    li{
      &:nth-child(1)::after{
      content: "";
      display: inline-block;
      position: absolute;
      top: .02rem;
      left: 33.3%;
      height: 0.3rem;
      width: 0.01rem;
      background: linear-gradient(to top, #fff, $border-color, #fff);
      }
      &:nth-child(2)::after{
      content: "";
      display: inline-block;
      position: absolute;
      top: .02rem;
      left: 66.6%;
      height: 0.3rem;
      width: 0.01rem;
      background: linear-gradient(to top, #fff, $border-color, #fff);
      }
    }
    .buttom-li {
      flex-basis: 33.3%;
    }
    .buttom-add{
        color: red;
    }
  }
   .swiper-container {
    padding-top: 0.4rem;
    margin-bottom: $base-h;
  }
  .investing{
    margin-top: .1rem;
    background: #fff;
    .logo{
      display: flex;
      justify-content: flex-end;
      padding: 0 .15rem;
      height: .22rem;
      >div{
        width: .17rem;
        height: .22rem;
        background: url(../../assets/money/mark.png) no-repeat;
        background-size: 100%;
        text-align: center;
        font-size: $sf;
        color: #fff;
      }
    }
    .main{
      display: flex;
      justify-content: space-between;
      padding: 0 .15rem .18rem .15rem;
     >div{
       flex: 1 0 33.3%;
     }
     .left,.center{
       border-right: 0.01rem solid #E8E8E8;
     }
      img{
        width: .15rem;
        height: .15rem;
        margin-right: .02rem;
      }
      .staus{
        display: inline-block;
        font-size:$mf ;
        color: #404B5F;
      }
      .staus-p{
        @extend .staus;
        padding-left: .18rem;
      }
      .grad{
        color: #E8C763;
      }
      .product{
        margin-top: .07rem;
        padding-left: .18rem;
        font-size: $sf;
        color: #666;
      }
    }
  }
  .container{
    padding: 0;
  }
  .continue-box {
    margin-top: -0.2rem;
    flex-direction: column;
    height: calc(100vh - 2rem);
    p {
      margin-top: 0.1rem;
      line-height: 0.3rem;
    }
    img {
      width: 1.03rem;
      height: 1.3rem;
    }
    .continue-invest {
      width: 1.6rem;
      line-height: 0.44rem;
      text-align: center;
      color: #fff;
    }
  }
}

</style>



