<template>
    <div class="contract-principal" v-if="contractShow.isPrincipal">
        <p class="tc title">出借本金确认书</p>
        <p>致：</p>
        <p>尊敬的客户</p>
        <br>
        <p>甲 方：</p>
        <p>姓 名： {{contract.userName}}</p>
        <p>身份证号：{{contract.userIdCard}}</p>
        <br>
        <p>甲方于
            <span v-if="!underLine">{{contract.contractCreateAt|dateText}}</span>
            <span v-if="underLine">XXXX年XX月XX日</span>与乙方签订了编号为编号{{contract.constractCode}}的《出 借咨询与服务协议》，意向投资金额为人民币
            <span class="tdl">{{money}}</span>
            <span v-if="underLine">____________</span> 元。现乙方对甲方实 际投资信息确认如下：甲方实际支付或划扣的金额为：人民币（大写）
            <span class="tdl" v-if="!underLine">{{money|up}}</span>
            <span v-if="underLine">____________</span>（¥
            <span class="tdl">{{money}}</span>
            <span v-if="underLine">____________</span>）。双方确认以上述实际支付或划扣金额作为甲 方在《出借咨询与服务协议》项下出借金额。
        </p>
        <br>
        <p>本确认书在支付或划扣完成之日生成，甲方可以登录聚金融APP查询。本确认 书作为《出借咨询与服务协议》的组成部分。如甲方对本确认书内容有任何疑议， 应在资金划扣成功之日起5个工作日内联系乙方，否则即视为对本确认书内容的 确认。
        </p>
    </div>
</template>

<script>
export default{
  name: 'principal-contract',
  props: {contractShow: Object, contract: Object, money: String, underLine: Boolean},
  data () {
    return {
    }
  },
  methods: {}
}
</script>

<style lang="scss" scoped>
</style>



