<template>
  <div class="case">
    <img src="../assets/login/useIphone.png" class="logo">
    <input type="text" class="text" :placeholder="iphone.title" v-model="iphone.main" @input="z">
  </div>
</template>

<script>
export default {
  props: {
    iphone: {
      type: Object,
      default: function () {
        return {
          title: '输入11位手机号',
          main: ''
        }
      }
    }
  },
  methods: {
    z: function () {
      let a = /\D/
      this.iphone.main = this.iphone.main.replace(a, '')
      this.$emit('isF', this.iphone.main)
    }
  }
}
</script>

<style lang="scss" scoped>
.case{
  display: flex;
  align-items: center;
  height: .6rem;
  font-family: PingFang-SC-Medium;
  border-bottom: 0.01rem solid $base-bg;
  background: #fff;
  .logo{
    height: .18rem;
    padding: 0 .15rem;
    border-right: .01rem solid $base-bg;
  }
  .text{
    flex: 1 0;
    margin-left: .1rem;
    font-size: $tip-f;
  }
}
</style>



