<template>
    <div class="body bt8">
      <div class="idCard">
        <div class="title">
          <img src="../../assets/my/idcard.png" >
          <p>上传身份证正反面</p>
        </div>
        <div class="drawing">
          <input type="file" accept="image/*" @change="upright($event)" id="up">
          <input type="file" accept="image/*" @change="bottomright($event)" id="bottom">
          <label for="up">
            <img :src="upImg" >
          </label>
          <label for="bottom">
            <img :src="bottomImg" >
          </label>
        </div>
      </div>
      <button class="id-button" :class="{nd: isOn}">提交</button>
      <p class="message" v-if="isOn">请上传图片</p>
    </div>
</template>

<script>
import imgZ from '@/assets/my/img.png'
import imgF from '@/assets/my/imgfan.png'
export default {
  data () {
    return {
      upImg: imgZ,
      bottomImg: imgF,
      isOn: true
    }
  },
  methods: {
    upright: function (e) {
      let a = e.target.files[0] // 读取上传文件
      console.log(a)
      let img = new FormData() // 转换formdata对象
      img.append('file', a)
      console.log(img)
      let params = { // 定义上传对象
        imageName: 'frontCard',
        realImage: img
      }
      console.log(this.path.myRealCard)
      let b = localStorage.getItem('id')
      this.$http(this.path.myRealCard(b, params))
        .then((rex) => {
          console.log(rex)
        })
    },
    bottomright: function (e) {
      let a = e.target.files[0]
      console.log(a)
      let img = new FormData()
      img.append('file', a)
      let params = {
        imageName: 'reverseCard',
        realImage: img
      }
      // console.log(this.path.myRealCard)
      let b = localStorage.getItem('id')
      this.$http(this.path.myRealCard(b, params))
        .then((rex) => {
          console.log(rex)
        })
    }
  },
  beforeCreate () {
    this.$parent.head.title = '实名认证'
    console.log(this.$route.query)
  }
}
</script>

<style lang="scss" scoped>
.body{
  @include center($head-h,0);
  background: $base-bg;
  font-family: PingFang-SC-Medium;
  padding-top: .1rem;
  .idCard{
    padding: 0 .15rem;
    background: #fff;
    .title{
      display: flex;
      align-items: center;
      height: .39rem;
      border-bottom: .01rem solid #D9D9D9;
      img{
        width: .19rem;
        margin-right: .05rem;
      }
      p{
        font-size: $tip-f;
        color: #666;
      }
    }
    .drawing{
      display: flex;
      justify-content: space-between;
      padding: .13rem 0;
      img{
        width: 1.65rem;
        height: 1.15rem;
      }
    }
  }
   .id-button{
    margin: .3rem .1rem 0 .1rem;
    width: calc(100vw - .1rem - .1rem);
    line-height: .44rem;
    background: $btn-bg;
    font-size: $enter-f;
    color: #fff;
  }
  .nd{
    background: #999;
  }
  #up{
    display: none;
  }
  #bottom{
    display: none;
  }
  .nd{
    background: #999;
  }
  .message{
    position: fixed;
    bottom: 1rem;
    left: 50%;
    margin-left: -$msg-w/2;
    width: $msg-w;
    line-height: $msg-h;
    text-align: center;
    background: $msg-bg;
    font-size: $msg-f;
    color: #fff;
  }
}
</style>



