<template>
    <div class="body">
       <img :src="rex" >
    </div>
</template>

<script>
export default{
  data () {
    return {
      rex: ''
    }
  },
  beforeCreate () {
    this.$parent.head = {
      title: '关于我们' // 标题内容
    }
    this.$http(this.path.myOur)
      .then(rex => {
        console.log(rex)
        this.rex = rex.data.date.dataValue
      }
      )
  }
}
</script>

<style lang="scss" scoped>
.body{
  @include center($head-h,0);
  img{
      width: 100%;
  }
}
</style>



