<template>
    <div class='body'>
        <div class="message">
            <div class="head">
                <p class="title">投资成功</p>
                <p class="data">2016-11-07</p>
            </div>
            <div class="main">
                <p class="info">新手体验计划已购买成功</p>
                <i class="fa fa-angle-right tr"></i>
            </div>
        </div>
    </div>
</template>

<script>
export default {
  beforeCreate () {
    this.$parent.head = {
      isWhite: false, // 箭头和标题颜色
      isEnter: false, // 标题字体
      title: '消息中心', // 标题内容
      isArrow: false, // 箭头显隐
      back: () => { // 点击返回
        this.$router.back(-1)
      }
    }
    let e = localStorage.getItem('id')
    this.$http(this.path.myMessage(e))
      .then(rex => {
        console.log(rex)
      })
  }
}
</script>

<style lang="scss" scoped>
.body{
    @include center($head-h,0);
    background: $base-bg;
    .message{
        margin: .1rem 0;
        background: #fff;
        .head{
            display: flex;
            justify-content: space-between;
            align-items: center;
            height: .33rem;
            padding: 0 .15rem ;
            border-bottom: .01rem solid $base-bg;
            .title{
                color:  $news-color;
            }
            .data{
                font-size: $sf;
                color: $list-color;
            }
        }
        .main{
            display: flex;
            justify-content: space-between;
             align-items: center;
            height: .46rem;
            padding: 0 .15rem ;
            .info{
                font-size: $sf;
                font-family: Adobe Heiti Std R;
            }
            i{
                flex-basis: .4rem;
                line-height: .5rem;
                font-size: $sf;
                color: #D4D4D4;
            }
        }
    }
}
</style>



