// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import router from './router'
import axios from 'axios' // axios请求插件
import path from './components/_path.js' // http请求
import './assets/scss/font-awesome.css' // 阿里图标库
import './assets/scss/reset.css' // 全局样式

Vue.config.productionTip = false
/* eslint-disable no-new */
Vue.prototype.path = path
Vue.prototype.$http = axios
Vue.filter('date', function (input) { // 日期-
  let d = new Date(input)
  let year = d.getFullYear()
  let month = d.getMonth() + 1
  let day = d.getDate() < 10 ? '0' + d.getDate() : '' + d.getDate()
  return year + '-' + month + '-' + day
})
Vue.filter('datePoint', function (input) { // 日期.
  let d = new Date(input)
  let year = d.getFullYear()
  let month = d.getMonth() + 1
  let day = d.getDate() < 10 ? '0' + d.getDate() : '' + d.getDate()
  return year + '.' + month + '.' + day
})
Vue.filter('dateText', function (input) { // 日期年月日
  let d = new Date(input)
  let year = d.getFullYear()
  let month = d.getMonth() + 1
  let day = d.getDate() < 10 ? '0' + d.getDate() : '' + d.getDate()
  return year + '年' + month + '月' + day + '日'
})
Vue.filter('rateOfInterest', function (input) { // 起息日期
  switch (input) {
    case 0:
      return ('T+0')
    case 1:
      return ('T+1')
    case 2:
      return ('T+2')
  }
})
Vue.filter('refundStyle', function (input) { // 还款方式
  switch (input) {
    case 0:
      return ('按月付息到期还本')
    case 1:
      return ('本息一次还')
  }
})
Vue.filter('investStatus', function (input) { // 投资状态
  switch (input) {
    case 0:
      return ('投资中')
    case 1:
      return ('回款中')
    case 2:
      return ('已回款')
  }
})
Vue.filter('moneyStatus', function (input) { // 理财状态
  switch (input) {
    case 0:
      return ('理财中')
    case 1:
      return ('退出中')
    case 2:
      return ('已退出')
  }
})
Vue.filter('mark', function (input) { // 角标
  switch (input) {
    case 1:
      return '新'
    case 2:
      return '热'
  }
})
Vue.filter('id18', function (value) { // 身份证过滤
  if (value) {
    if (value.length === 15) {
      return value.substr(0, 3) + '*********' + value.substr(11, 5)
    } else {
      return value.substr(0, 3) + '*********' + value.substr(14, 5)
    }
  }
})
Vue.filter('thousand', function (input) { // 千分位和.00
  return Number(input).toFixed(2).replace(/(\d)(?=(\d{3})+\.)/g, '$1,')
})
Vue.filter('bankCard', function (val) { // 银行卡过滤器
  if (val) {
    return val.substr(0, 4) + '****' + '****' + '****' + val.substr(11, 4)
  }
})
Vue.filter('lastNum', function (input) { // 银行卡尾号
  return String(input).slice(String(input).length - 4)
})
Vue.filter('deadLine', function (input) { // 期限判断
  if (input >= 30) {
    return input / 30
  } else {
    return input
  }
})
Vue.filter('deadWord', function (input) { // 月，天判断
  if (input >= 30) {
    return '月'
  } else {
    return '天'
  }
})
Vue.filter('up', function (n) { // 金额大写
  let fraction = ['角', '分']
  let digit = ['零', '壹', '贰', '叁', '肆', '伍', '陆', '柒', '捌', '玖']
  let unit = [
    ['元', '万', '亿'],
    ['', '拾', '佰', '仟']
  ]
  let head = n < 0 ? '欠' : ''
  n = Math.abs(n)
  let s = ''
  for (let i = 0; i < fraction.length; i++) {
    s += (digit[Math.floor(n * 10 * Math.pow(10, i)) % 10] + fraction[i]).replace(/零./, '')
  }
  s = s || '整'
  n = Math.floor(n)
  for (let i = 0; i < unit[0].length && n > 0; i++) {
    let p = ''
    for (let j = 0; j < unit[1].length && n > 0; j++) {
      p = digit[n % 10] + unit[1][j] + p
      n = Math.floor(n / 10)
    }
    s = p.replace(/(零.)*零$/, '').replace(/^$/, '零') + unit[0][i] + s
  }
  return head + s.replace(/(零.)*零元/, '元').replace(/(零.)+/g, '零').replace(/^整$/, '零元整')
})

new Vue({
  el: '#app',
  router,
  axios,
  components: {
    App
  },
  template: '<App/>'
})



