<template>
    <div class='money-view btd9 tabBox'>
        <ul class='tag flex-bet tc bbe8'>
            <li class="swiper-button-prev" slot="button-prev">产品列表</li>
            <li class="swiper-button-next" slot="button-next">产品续投</li>
        </ul>
        <swiper :options="swiperOption">
            <swiper-slide>
                <ul class='product-list container' v-for='item in product' :key='item.id' @click='goProductDetail(item.id)'>
                    <li class='mark' v-if='item.mark'>
                        <span>{{item.mark|mark}}</span>
                        <img src='../../assets/money/mark.png' alt=''>
                    </li>
                    <li class='list-one'>{{item.productName}}</li>
                    <li class='flex-bet list-two'>
                        <span>{{item.interestRate*100|thousand}}</span>
                        <span>{{item.deadline|deadLine}}</span>
                        <span>{{item.investmentAmount|thousand}}</span>
                    </li>
                    <li class='flex-bet list-three'>
                        <span>预期年化 (%)</span>
                        <span>理财期限 ({{item.deadline|deadWord}})</span>
                        <span>起投金额 (元)</span>
                    </li>
                </ul>
            </swiper-slide>
            <swiper-slide>
                <ul v-if="!isContinue" class='continue-list container' v-for='item in continued' :key='item.id' @click='goInvestDetail(item.id)'>
                    <li class='mark' v-if='item.mark'>
                        <span>{{item.mark|mark}}</span>
                        <img src='../../assets/money/mark.png' alt=''>
                    </li>
                    <li class='list-one'>{{item.productName}}</li>
                    <li class='list-two'>
                        <span>{{item.interestRate*100|thousand}}</span>
                        <span>{{item.money|thousand}}</span>
                    </li>
                    <li class='list-three'>
                        <span>预期年化 (%)</span>
                        <span>起投金额 (元)</span>
                    </li>
                    <li class='list-four bte8'>{{item.startAt|date}}起息 - {{item.endAt|date}}到期</li>
                </ul>
                <div class='flex-cent continue-box' v-if='isContinue'>
                    <img src='../../assets/money/continue.png' alt='立即投资'>
                    <span class='tip'>暂无可续投产品，赶紧投资吧</span>
                    <div class="swiper-pagination">
                        <div class='btn continue-invest'>立即投资</div>
                    </div>
                </div>
            </swiper-slide>
        </swiper>
    </div>
</template>

<script>
import 'swiper/dist/css/swiper.css'
import { swiper, swiperSlide } from 'vue-awesome-swiper'
export default{
  data () {
    return {
      product: {}, // 产品列表
      continued: {}, // 续投列表
      isContinue: true, // 是否显示立即投资按钮
      swiperOption: { // swiper
        pagination: {
          el: '.swiper-pagination',
          clickable: true,
          type: 'custom',
          bulletClass: 'continue-invest'
        },
        observer: true,
        observerParents: true,
        navigation: { // 点击切换
          nextEl: '.swiper-button-next',
          prevEl: '.swiper-button-prev'
        }
      }
    }
  },
  created () {
    this.$parent.head.isHead = true // 头部显示
    this.$parent.head.title = '理财列表' // 理财列表
    this.$parent.head.isBell = true // 铃铛隐藏
    this.$parent.head.isWhite = false // 标题不变色
    this.$parent.head.isEnter = false // 标题字体不变
    this.$parent.foot.isMoney = true // 底部按钮变色
    this.$http(this.path.product).then(res => { // 产品
      console.log(res.data.code)
      if (res.data.code === 0) {
        this.product = res.data.data
        console.log(this.product)
      }
    })
    this.$http(this.path.continueList).then(res => { // 续投
      console.log(res.data.code)
      if (res.data.code === 0) {
        this.continued = res.data.data
        console.log(this.continued)
      } else {
        this.isContinue = false // 立即投资隐藏
      }
    })
  },
  methods: {
    goProductDetail (id) { // 去产品详情
      this.$router.push({
        name: `productDetail`,
        query: {
          id: id
        }
      })
    },
    goInvestDetail (id) { // 去续投详情
      this.$router.push({
        name: `investDetail`,
        query: {
          id: id,
          show: true // 是否是续投进入的
        }
      })
    }
  },
  components: {
    swiper,
    swiperSlide
  }
}
</script>

<style lang='scss' scoped>
.money-view {
  @include center($head-h, $base-h);
  font-family: "PingFang-SC-Medium";
  background: $base-bg;
  .tag {
    position: fixed;
    left: 0;
    right: 0;
    background: #fff;
    z-index: 2;
    li {
      flex-basis: 50%;
    }
    .swiper-button-next::before {
      content: "";
      position: absolute;
      left: 0;
      height: 0.4rem;
      width: 0.01rem;
      background: linear-gradient(to top, #fff, $border-color, #fff);
    }
  }
  .mark {
    height: 0;
    color: #fff;
    text-align: right;
    font-size: 0.12rem;
    span {
      position: absolute;
      top: 0;
      right: 0.24rem;
    }
  }
  .product-list {
    position: relative;
    margin-bottom: 0.2rem;
    background: #fff;
    .list-one {
      font-size: 0.16rem;
      line-height: 0.5rem;
    }
    .list-two {
      line-height: 0.2rem;
      font-size: 0.15rem;
      & span:first-child {
        font-size: 0.24rem;
        color: $title-color;
      }
      & span:nth-child(1)::after {
        content: "";
        position: absolute;
        left: 33.3%;
        height: 0.35rem;
        width: 0.01rem;
        background: #e8e8e8;
      }
      & span:nth-child(2)::after {
        content: "";
        position: absolute;
        left: 66.66%;
        height: 0.35rem;
        width: 0.01rem;
        background: #e8e8e8;
      }
    }
    .list-three {
      line-height: 0.5rem;
      font-size: 0.13rem;
      color: $tip-color;
    }
  }
  .continue-list {
    position: relative;
    margin-bottom: 0.2rem;
    background: #fff;
    .list-one {
      font-size: 0.16rem;
      line-height: 0.5rem;
      .line::after {
        content: "";
        position: absolute;
        left: 50%;
        margin-top: 0.15rem;
        height: 0.35rem;
        width: 0.01rem;
        background: #e8e8e8;
        opacity: 0.62;
      }
    }
    .list-two {
      display: flex;
      line-height: 0.2rem;
      font-size: 0.15rem;
      & span:first-child {
        flex-basis: 40%;
        font-size: 0.24rem;
        color: $title-color;
      }
      & span:nth-child(2) {
        flex-basis: 60%;
      }
      & span:first-child::after {
        content: "";
        position: absolute;
        left: 33.3%;
        height: 0.35rem;
        width: 0.01rem;
        background: #e8e8e8;
      }
    }
    .list-three {
      display: flex;
      line-height: 0.5rem;
      font-size: 0.13rem;
      color: $tip-color;
      & span:first-child {
        flex-basis: 40%;
      }
      & span:nth-child(2) {
        flex-basis: 60%;
      }
    }
    .list-four {
      line-height: 0.25rem;
      color: $list-color;
      font-size: 0.12rem;
      margin: 0 -0.2rem;
      padding: 0 0.2rem;
    }
  }
}
.continue-box {
  margin-top: -0.2rem;
  flex-direction: column;
  height: calc(100vh - 2rem);
  span {
    margin-top: 0.1rem;
    line-height: 0.3rem;
  }
  .continue-invest {
    width: 1.6rem;
    line-height: 0.44rem;
    text-align: center;
    color: #fff;
  }
}
.swiper-container {
  padding-top: 0.6rem;
  min-height: 100%;
}
.swiper-button-prev,
.swiper-button-next {
  position: relative;
  right: 0;
  left: 0;
  top: 0;
  margin: 0;
  height: 100%;
  opacity: 1;
  background: #fff;
  outline: none;
}
.swiper-button-prev.swiper-button-disabled,
.swiper-button-next.swiper-button-disabled {
  color: $title-color;
  border-bottom: 0.02rem solid $title-color;
  opacity: 1;
}
.swiper-slide {
  height: 0;
}
.swiper-slide-active {
  height: auto;
}
.swiper-pagination {
  position: relative;
  width: 1.6rem;
  bottom: 0;
}
</style>



