<template>
  <div class="body">
    <img :src="img" class="bte8">
  </div>
</template>

<script>
export default {
  data () {
    return {
      img: ''
    }
  },
  beforeCreate () {
    console.log(this.$route.query)
    let a = this.$route.query
    this.$parent.head.title = a.title
  },
  created () {
    this.img = this.$route.query.details
  }
}
</script>

<style lang="scss" scoped>
.body{
  @include center($head-h, $base-h);
  font-family:PingFang-SC-Medium;
  img{
    width: 100vw;
  }
}
</style>



