<template>
    <div class="body">
      <textarea name="ccc" id="text"   v-model="desc" placeholder = '请输入你宝贵的意见，我们将及时反馈你' @input="warmX">
      </textarea>
      <div class="message">
          <span :class="{red: warmT}">{{desc.length}}</span>
          <span>/240</span>
      </div>
      <transition name=fade>
      <p class="msg msg-x" v-show="isMsg">提交成功</p>
      </transition>
      <transition name=fade>
       <p class="msg msg-x" v-show="isMor">字数过多</p>
      </transition>
      <button @click="push">提交</button>
    </div>
</template>

<script>
export default{
  data () {
    return {
      entry: 0,
      desc: '',
      warmT: false,
      isMsg: false,
      isMor: false
    }
  },
  methods: {
    warmX () {
      if (this.desc) {
        if (this.desc.length > 240) {
          this.warmT = true
        } else {
          this.warmT = false
        }
      }
    },
    push () {
      if (this.warmT) {
        this.isMor = true
        setInterval(
          () => { this.isMor = false }, 2000)
      } else {
        console.log(this.desc)
        var a = {
          content: this.desc
        }
        let b = localStorage.getItem('id')
        this.$http(this.path.myIeda(b, a))
          .then((res) => {
            console.log(res)
            if (res.data.code === 0) {
              this.isMsg = true
              setInterval(
                () => { this.isMsg = false }, 2000)
            }
          })
      }
    }
  },
  computed: {
  },
  beforeCreate () {
    this.$parent.head = {
      title: '意见反馈' // 标题内容
    }
  }
}
</script>

<style lang="scss" scoped>
.body{
  @include center($head-h,0);
  padding: .1rem;
         background: $base-bg;
  #text{
     background: #fff;
      width: calc(100vw - .11rem - .11rem);
      height: 3rem;
      margin: 0 auto;
      font-size: $head-f;
  }
  .message{
      float: right;
  }
   >button{
    display: block;
    position: fixed;
    bottom: .3rem;
    left: .14rem;
    right: .14rem;
    height: .44rem;
    width: calc(100vw - 0.2rem - 0.2rem);
    font-size: $enter-f;
    color: #fff;
    background: $btn-bg;
    }
    .red{
        color: red;
        font-weight: bold;
    }
}
</style>



