<template>
    <div class="body">
       <div class="message" v-for="item of list" :key="item.index" @click="go(item.link)">
           <div>
               <i class="logo"></i>
               <p class="title">{{item.title}}</p>
           </div>
           <div>
               <p class="size">{{item.size}}</p>
               <i class="fa fa-angle-right"></i>
           </div>
       </div>
       <button @click="out">退出</button>
       <transition name="fade">
         <pop-window :pop='pops'></pop-window>
       </transition>
    </div>
</template>

<script>
import popWindow from '../pop.vue'
export default{
  data () {
    return {
      list: [
        {
          title: '帮助中心',
          link: 'myHelp'
        },
        {
          title: '关于我们',
          link: 'myOur'
        },
        {
          title: '意见反馈',
          link: 'myIeda'
        },
        {
          title: '清除缓存',
          link: 'clear',
          size: '125KB'
        },
        {
          title: '版本更新',
          link: 'updata'
        }
      ],
      pops: {
        title: '发现新版本标题',
        doText: '更新',
        callback: function () {
          console.log(1)
        },
        main: '发现新版本',
        isOn: false
      }
    }
  },
  methods: {
    go (url) {
      if (url === 'updata' || url === 'clear') {
        if (url === 'updata') { // 当点击了系统升级后的操作
          this.up()
        } else { // 点击清除缓存后的操作
          this.clearLoc()
        }
      } else {
        this.$router.push({name: url})
      }
    },
    up () { // 系统升级请求
      this.$http(this.path.myUpdata)
        .then((rex) => {
          console.log(rex)
        })
      this.pops = {
        doText: '更新',
        title: '系统升级',
        isOn: true,
        main: ['发现新版本'],
        callback: function () {
          console.log('更新')
        }
      }
    },
    clearLoc () { // 清除缓存
      console.log(1)
      this.pops = {
        doText: '确定',
        title: '',
        isOn: true,
        main: ['是否清除缓存'],
        callback: function () {
          console.log('清除')
          this.isOn = false
        }
      }
    },
    out: function () {
      this.pops = {
        doText: '确定',
        title: '',
        isOn: true,
        main: ['是否登出'],
        callback: () => {
          // console.log('清除')
          this.$http(this.path.out)
            .then((rex) => {
              console.log(rex)
              if (rex.data.code === 0) {
                this.$router.push({
                  name: 'enter'
                })
              }
            })
          // this.isOn = false
        }
      }
    }
  },
  components: {
    popWindow
  },
  beforeCreate () {
    this.$parent.head = {
      isWhite: false, // 箭头和标题颜色
      isEnter: false, // 标题字体
      title: '更多设置', // 标题内容
      isArrow: false, // 箭头显隐
      back: () => { // 点击返回
        this.$router.back(-1)
      }
    }
  }
}
</script>

<style lang="scss" scoped>
   .body{
    @include center($head-h,0);
    background: $base-bg;
    font-family: PingFang-SC-Medium;
    >div{
          &:nth-child(1) .logo{
                    background: url(../../assets/my/esc/help.png) no-repeat;
                    background-size: 100%;
        }
          &:nth-child(2) .logo{
                    background: url(../../assets/my/esc/our.png) no-repeat;
                    background-size: 100%;
        }
         &:nth-child(3) .logo{
                    background: url(../../assets/my/esc/ieda.png) no-repeat;
                    background-size: 100%;
        }
         &:nth-child(4) .logo{
                    background: url(../../assets/my/esc/loc.png) no-repeat;
                    background-size: 100%;
        }
         &:nth-child(5) .logo{
                    background: url(../../assets/my/esc/updata.png) no-repeat;
                    background-size: 100%;
        }
    }
    >button{
    display: block;
    position: fixed;
    bottom: .3rem;
    left: .14rem;
    right: .14rem;
    height: .44rem;
    width: calc(100vw - 0.14rem - 0.14rem);
    font-size: $enter-f;
    color: #fff;
    background: $btn-bg;
    }
    .message{
        &:nth-child(1){
            margin-top: .1rem;
        }
        div{
            display: flex;
            align-items: center;
            .logo{
                width: .23rem;
                height: .23rem;
                margin-right: .1rem;
            }
            .title{
                font-size: $mf;
                color: #999;
            }
            .size{
                margin-right: .1rem;
                font-size: $sf;
                color: #999;
            }
        }
        display: flex;
        align-items: center;
        justify-content: space-between;
        height: .6rem;
        padding: 0 .16rem;
        border-top: .01rem solid $base-bg;
        background: #fff;
        .fa-angle-right{
            font-size: .2rem;
            color:#D4D4D4 ;
        }
    }
}
</style>



