<template>
    <div class="continued-view bte8">
        <ul class="product-list container">
            <li class="mark" v-if="detail.mark">
                <span>{{detail.mark|mark}}</span>
                <img src="../../assets/pay/mark.png" alt="">
            </li>
            <li class="list-one">{{detail.productName}}</li>
            <li class="flex-bet list-two">
                <span>{{detail.interestRate*100|thousand}}</span>
                <span>{{detail.deadline|deadLine}}</span>
                <span>{{detail.investmentAmount|thousand}}</span>
            </li>
            <li class="flex-bet list-three">
                <span>预期年化 (%)</span>
                <span>理财期限 ({{detail.deadline|deadWord}})</span>
                <span>起投金额 (元)</span>
            </li>
        </ul>
        <div class="bank flex-bet" v-if="isCard" @click="changeCard()">
            <div class=" flex-bet">
                <img :src="pay.icon" class="bank-logo">
                <div class="bank-num">
                    <p>
                        <span class="bank-name">{{pay.bankName}}</span>
                        <span class="last-num">(尾号{{pay.bankCard|lastNum}})</span>
                    </p>
                    <p class="bank-tip">单笔限额{{pay.singleLimited/10000}}万元，单日限额{{pay.dayLimited/10000}}万元</p>
                </div>
            </div>
            <i class="fa fa-angle-right"></i>
        </div>
        <div class="bank flex-bet" v-if="!isCard" @click="addCard()">
            <div class=" flex-bet">
                <img src="../../assets/pay/add.png" alt="">
                <div class="bank-num">
                    <p class="bank-name">添加银行卡</p>
                    <p class="bank-tip">持卡人需与投保人保持一致</p>
                </div>
            </div>
            <i class="fa fa-angle-right"></i>
        </div>
        <div class="input-money container">
            <span>金额</span>
            <input type="text" v-model="inputMoney" @input="input()" placeholder="请填写投资金额">
        </div>
        <p class="agree-line">
            <label for="choose" v-if="isChoose">
                <img src="../../assets/money/agree.png" alt="">
            </label>
            <label for="choose" v-else>
                <img src="../../assets/money/refuse.png" alt="">
            </label>
            <input type="checkbox" id="choose" v-model="isChoose">
            <span class="agree">本人已认真阅读并同意</span>
            <span class="blue" @click="checkContract()">《出借合同与服务协议》</span>
        </p>
        <transition name="slide-fade">
            <div class="msg-box tc" v-show="isInteger">
                <span class="msg">{{tip}}</span>
            </div>
        </transition>
        <div class="bottom-money flex-bet container">
            <p>
                <span>本次共需支付</span>
                <span class="money-num">￥{{inputMoney|thousand}}</span>
            </p>
            <button :disabled="!isChoose" :class="[{'btn-grey':!isChoose}, 'btn']" @click="confirm()">确定</button>
        </div>
        <transition name="slide-fade">
            <div class="add-alert alert" @click.self="boxHide()" v-if="chooseCard">
                <div class="card-box">
                    <div class="add-bank type tc">
                        选择支付方式
                        <i class="fa fa-close" @click="close()"></i>
                    </div>
                    <div class="add-bank flex-bet" :class="{'grey-bg':cardList.isOne}" @click="clickCard('isOne')">
                        <div class=" flex-bet">
                            <img :src="pay.icon" class="bank-logo">
                            <div class="bank-num">
                                <p>
                                    <span class="bank-name">{{pay.bankName}}</span>
                                    <span class="last-num">(尾号{{pay.bankCard|lastNum}})</span>
                                </p>
                                <p class="bank-tip">单笔限额{{pay.singleLimited/10000}}万元，单日限额{{pay.dayLimited/10000}}万元</p>
                            </div>
                        </div>
                        <i class="fa fa-check" v-show="cardList.isOne"></i>
                    </div>
                    <div class="add-bank flex-bet" v-if="isTwo" :class="{'grey-bg':cardList.isTwo}" @click="clickCard('isTwo')">
                        <div class=" flex-bet">
                            <img :src="payTwo.icon" class="bank-logo">
                            <div class="bank-num">
                                <p>
                                    <span class="bank-name">{{payTwo.bankName}}</span>
                                    <span class="last-num">(尾号{{payTwo.bankCard|lastNum}})</span>
                                </p>
                                <p class="bank-tip">单笔限额{{payTwo.singleLimited/10000}}万元，单日限额{{payTwo.dayLimited/10000}}万元</p>
                            </div>
                        </div>
                        <i class="fa fa-check" v-show="cardList.isTwo"></i>
                    </div>
                    <div class="add-bank flex-bet" v-if="!isTwo" @click="addCard()">
                        <div class=" flex-bet">
                            <img src="../../assets/pay/addTwo.png" alt="">
                            <div class="bank-num">
                                <p class="bank-name">添加银行卡</p>
                                <p class="bank-tip">持卡人需与投保人保持一致</p>
                            </div>
                        </div>
                        <i class="fa fa-angle-right"></i>
                    </div>
                </div>
            </div>
        </transition>
    </div>
</template>

<script>
export default{
  data () {
    return {
      id: '',
      detail: {}, // 产品详情

      tip: '', // 提示文字
      isCard: false, // 是否有卡,默认没有显示添加卡
      isTwo: false, // 第二张，默认没有，显示添加卡
      chooseCard: false, // 弹窗选卡
      cardList: {
        isOne: false, // 卡1 未选中
        isTwo: false // 卡2 未选择
      }, // 卡列表
      isChoose: true, // 是否同意合同
      isInteger: false, // 整数提示
      inputMoney: '', // 输入的金额
      pay: undefined, // 支付详情
      payTwo: undefined, // 支付详情2
      back: () => { // 点击返回
        this.$router.back(-1)
      }
    }
  },
  created () {
    this.getProduct() // 获得产品详情
    this.$http(this.path.productDetail(this.id)).then(res => {
      console.log(res.data.code, this.detail)
      if (res.data.code === 0) {
        this.detail = res.data.data
        console.log(this.detail)
      }
    })
    this.$http(this.path.inputMoney).then(res => { // 投资数据
      console.log(res.data.code)
      console.log(res.data.data.length)
      if (res.data.code === 0) {
        this.pay = res.data.data[0] // 卡1z
        console.log(this.pay)
      }
      if (res.data.data.length > 1) {
        this.payTwo = res.data.data[1] // 卡2
        console.log(this.payTwo)
      } else {
        this.payTwo = undefined
      }
      if (this.pay.id) { // 如果有卡
        this.isCard = true // 添加卡隐藏,显示卡1
      } else {
        this.isCard = false // 显示添加卡
      }
    })
    this.$parent.head.title = '投资金额'
    this.$parent.head.isWhite = false
    this.$parent.head.isEnter = false
    this.$parent.head.isArrow = false
    this.cardList.isOne = true // 卡1默认选中
  },
  methods: {
    input () { // 输入整数
      this.inputMoney = this.inputMoney.replace(/\D/g, '')
    },
    getProduct () { // 获得id
      this.id = this.$route.query.id
    },
    confirm () { // 点击确定
      if (this.pay === undefined) {
        this.isInteger = true
        this.tip = '请先添加银行卡'
        return
      }
      console.log(this.inputMoney % 10000)
      if (this.inputMoney % 10000 !== 0 | this.inputMoney === '') { // 判断10000整数倍
        this.tip = '请输入10000的整数倍'
        this.isInteger = true // 弹框
      } else { // 跳转
        this.isInteger = false // 弹框消失
        this.$router.push({ // 去合同
          name: `contract`,
          params: {
            productId: this.detail.id, // 产品id
            money: this.inputMoney, // 金额
            bankCardId: this.pay.id, // 银行卡idthis.pay.id
            userSign: '' // 用户签名
          }
        })
      }
    },
    addCard () { // 添加卡，去添加银行卡页面
      this.$router.push({
        name: 'myAddBank'
      })
    },
    changeCard () { // 点击选卡
      this.chooseCard = true // 选卡弹窗，显示卡1
      if (this.payTwo) { // 如果有卡2
        this.isTwo = true // 卡2显示，添加卡消失
      } else { // 如果没有卡2
        this.isTwo = false // 卡2消失，添加卡显示
      }
    },
    clickCard (value) { // 点击卡选择切换
      for (let key in this.cardList) {
        if (value === key) { // 点击对应卡 背景变，显示√
          console.log(value)
          this.cardList[key] = true
          this.chooseCard = false // 弹窗消失
        } else { // 其他的恢复
          this.cardList[key] = false
        }
      }
      if (value === 'isOne') { // 渲染卡1
        console.log(1)
        this.isOne = this.isOne
      } else { // 渲染卡2
        console.log(2)
        this.isOne = this.isTwo
      }
    },
    boxHide () { // 点击灰色区域弹窗消失
      this.chooseCard = false
    },
    close () { // 点击关闭按钮弹窗消失
      this.chooseCard = false
    },
    checkContract () { // 查看合同
      this.$router.push({ // 去合同
        name: `contract`,
        query: {
          type: 2
        }
      })
    }
  }
}
</script>

<style lang="scss" scoped>
.continued-view {
  @include center($head-h, $base-h);
  font-family: "PingFang-SC-Medium";
  background: $base-bg;
  .product-list {
    position: relative;
    margin: 0.1rem 0;
    background: #fff;
    .list-one {
      line-height: 0.5rem;
    }
    .list-two {
      line-height: 0.1rem;
      font-size: 0.15rem;
      & span:first-child {
        font-size: 0.24rem;
        color: $title-color;
      }
      & span:nth-child(1)::after {
        content: "";
        position: absolute;
        left: 33.3%;
        height: 0.35rem;
        width: 0.01rem;
        background: #e8e8e8;
      }
      & span:nth-child(2)::after {
        content: "";
        position: absolute;
        left: 66.66%;
        height: 0.35rem;
        width: 0.01rem;
        background: #e8e8e8;
      }
    }
    .list-three {
      line-height: 0.5rem;
      font-size: 0.13rem;
      color: $tip-color;
    }
  }
  .mark {
    height: 0;
    color: #fff;
    text-align: right;
    font-size: 0.1rem;
    span {
      position: absolute;
      top: 0;
      right: 0.24rem;
    }
  }
  .bank {
    padding: 0.2rem;
    line-height: 0.25rem;
    background: #fff;
  }
  .input-money {
    margin: 0.1rem 0;
    line-height: 0.6rem;
    background: #fff;
    input {
      width: 2.2rem;
      text-indent: 0.1rem;
    }
    input::-webkit-input-placeholder {
      color: $list-color;
      font-size: 0.12rem;
    }
  }
  .agree-line {
    padding: 0 0.2rem;
    input {
      display: none;
    }
    .agree {
      font-size: 0.12rem;
      color: $tip-color;
    }
    .blue {
      font-size: 0.12rem;
      color: $agree-color;
    }
  }
  .msg-box {
    margin-top: 1rem;
  }
  .bottom-money {
    position: fixed;
    bottom: 0;
    left: 0;
    right: 0;
    line-height: 0.6rem;
    font-size: 0.15rem;
    background: #fff;
    .money-num {
      color: #d7a863;
    }
    .btn {
      width: 1rem;
    }
  }
  .add-alert {
    .card-box {
      position: absolute;
      left: 0;
      right: 0;
      bottom: 0;
    }
    .add-bank {
      padding: 0.05rem 0.2rem;
      line-height: 0.2rem;
      background: #fff;
      .fa-check {
        font-size: 0.2rem;
        color: $check-color;
        -webkit-text-stroke: 0.02rem #f6f6f6;
      }
    }
    .type {
      line-height: 0.4rem;
      color: $tip-color;
      font-size: 0.16rem;
      .fa-close {
        float: right;
        line-height: 0.35rem;
        font-size: 0.2rem;
        -webkit-text-stroke: 0.02rem #fff;
      }
    }
  }
  .bank-logo {
    width: 0.355rem;
    height: 0.355rem;
  }
  .fa-angle-right {
    font-size: 0.3rem;
    color: #d4d4d4;
    -webkit-text-stroke: 0.01rem #fff;
  }
  .bank-name {
    font-size: 0.16rem;
  }
  .bank-num {
    margin-left: 0.05rem;
    .last-num {
      color: $list-color;
    }
    .bank-tip {
      color: $list-color;
      font-size: 0.12rem;
    }
  }
  .grey-bg {
    background: #f6f6f6 !important;
  }
}
</style>



