<template>
  <div class="body bte8">
    <div v-if="isOn" class="main">
      <ul>
        <li class="list" v-for="item of list" :key='item.index'>
          <div class="one">
            <p>{{item.productName}}</p>
            <div>
              <span v-if="item.status">+</span>
              <span v-else>-</span>
              <span>{{item.money|thousand}}</span>
              <span class="yuan">元</span>
            </div>
          </div>
          <div class="two">
            <p>{{item.transactionAt|data8}}</p>
            <p v-if="item.status==0||item.status==2||item.status==4" class="green">{{item.status|status}}</p>
            <p v-else class="red">{{item.status|status}}</p>
          </div>
        </li>
      </ul>
    </div>
    <div v-else class="no">
      <img src="../../assets/money/continue.png" >
      <p>暂无交易内容</p>
    </div>
  </div>
</template>

<script>
export default {
  data () {
    return {
      isOn: false,
      list: ''
    }
  },
  beforeCreate () {
    this.$parent.head.title = '交易流水' // 标题内容
  },
  created () {
    this.$http(this.path.myWater)
      .then((rex) => {
        console.log(rex)
        this.list = rex.data.data
        if (this.list.length !== 0) {
          this.isOn = true
        }
      })
  },
  filters: {
    status: function (val) {
      switch (val) {
        case 0:
          return '付款成功'
        case 1:
          return '付款失败'
        case 2:
          return '回款成功'
        case 3:
          return '回款失败'
        case 4:
          return '返息成功'
        case 5:
          return '返息失败'
      }
    },
    data8: function timetrans (dat) {
      let date = new Date(dat)
      let Y = date.getFullYear() + '-'
      let M = (date.getMonth() + 1 < 10 ? '0' + (date.getMonth() + 1) : date.getMonth() + 1) + '-'
      let D = (date.getDate() < 10 ? '0' + (date.getDate()) : date.getDate()) + ' '
      let h = (date.getHours() < 10 ? '0' + date.getHours() : date.getHours()) + ':'
      let m = (date.getMinutes() < 10 ? '0' + date.getMinutes() : date.getMinutes()) + ':'
      let s = (date.getSeconds() < 10 ? '0' + date.getSeconds() : date.getSeconds())
      return Y + M + D + h + m + s
    }
  }
}
</script>

<style lang="scss" scoped>
.body{
   @include center($head-h,0);
    font-family: PingFang-SC-Medium;
    background: $base-bg;
    font-family: PingFang-SC-Medium;
    padding-top: .1rem;
    .main{
      .list{
        background: #fff;
        border-bottom: .01rem solid #e8e8e8;
        >div{
          display: flex;
          justify-content: space-between;
          align-items: center;
          padding: 0 .15rem;
        }
        .one{
          padding-top: .15rem;
          >p{
            font-size: $water-f;
            color: #333;
          }
          >div>span{
            font-size: $enter-f;
            color: #D3A55D;
          }
          .yuan{
            font-size: $sf;
            color: #999;
          }
        }
        .two{
        margin-top: .13rem;
        padding-bottom: .15rem;
        .green{
          color: #56C005;
          font-size: $water-f;
        }
        .red{
          color: #E1432F;
          font-size: $water-f;
        }
        }
      }
    }
    .no{
    position: absolute;
    top: 1.12rem;
    left: 50%;
    width: 1.23rem;
    margin-left: -0.61rem;
    text-align: center;
    color: #666;
    font-size: $sf;
    img{
      margin-bottom: .15rem;
    }
    }
}
</style>



