<template>
  <div class="recommend ">
      <swiper :options='swiperOption1' class="banner-logo">
          <swiper-slide v-for="item of banner" :key="item.index">
              <img :src="item.bannerCover" @click="banGo(item)">
          </swiper-slide>
      </swiper>
      <div class="swiper-pagination xing"></div>
      <swiper :options='swiperOption2' class="list-main" ref="x">
        <swiper-slide v-for="item of list" :key="item.index">
          <div class="list-box">
            <div class="title">
              <p>{{item.productName}}</p>
              <div v-if="item.mark" class="mark">{{item.mark|mark}}</div>
            </div>
            <div class="list">
              <p class="nian">预期年化</p>
              <p class="li">{{item.interestRate*100}}%</p>
              <div class="foot">
                <div>
                  <p>起购</p>
                  <p class="footp">{{item.investmentAmount|thousand}}元</p>
                </div>
                <div>
                  <p>期限</p>
                  <div>
                  <span>{{item.deadline|deadLine}}</span>
                  <span v-if="item.deadline > 30">个</span>
                  <span>{{item.deadline|deadWord}}</span>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </swiper-slide>
      </swiper>
      <button @click="tou" class="tou btn">立即投资</button>
    <div class="logo" v-if="isOn">
      <div class="isOn">
        <i class="fa fa-spin fa-spinner "></i>
        <p>加载中</p>
      </div>
    </div>
  </div>
</template>

<script>
import 'swiper/dist/css/swiper.css'
import { swiper, swiperSlide } from 'vue-awesome-swiper'

export default {
  data () {
    return {
      isOn: true,
      swiperOption1: {
        autoplay: true,
        pagination: {
          el: '.swiper-pagination',
          type: 'bullets',
          clickable: false
        }
      },
      swiperOption2: {
        observer: true,
        observerParents: true,
        autoHeight: true,
        scrollbarHide: true,
        effect: 'coverflow',
        indexW: '',
        on: {
          slideChangeTransitionStart: function () {
            console.log(this.activeIndex)
            this.indexW = this.activeIndex
            // console.log(this.indexW)
          }
        }
      },
      banner: '',
      list: ''
    }
  },
  methods: {
    tou: function () { // 跳转产品详情
      console.log(this.$refs.x.swiper.indexW)
      let e = this.$refs.x.swiper.indexW
      let a = this.list[e].id
      console.log(a)
      this.$router.push({
        name: 'productDetail',
        query: {id: a}
      })
    },
    banGo: function (item) {
      console.log(item)
      let title = item.title
      let details = item.details
      this.$router.push({
        name: 'banner',
        query: {
          title: title,
          details: details
        }
      })
    }
  },
  beforeCreate () {
    this.$parent.head.isHead = true // 头部显示
    this.$parent.head.title = '精品推荐' // 理财列表
    this.$parent.head.isBell = false // 铃铛隐藏
    this.$parent.head.isWhite = false // 标题不变色
    this.$parent.head.isEnter = false // 标题字体不变
    this.$parent.foot.isRecome = true // 底部按钮变色
  },
  created () {
    this.$http(this.path.banner) // 请求banner图
      .then((rex) => {
        console.log(rex)
        this.banner = rex.data
        console.log(this.banner)
        if (this.list) {
          this.isOn = false
          console.log(1)
        }
      })
    this.$http(this.path.product) // 请求消息列表
      .then((rex) => {
        console.log(rex)
        this.list = rex.data.data
        console.log(this.list)
        if (this.banner) {
          this.isOn = false
          console.log(2)
        }
        this.isOn = false
      })
  },
  components: {
    swiper,
    swiperSlide
  }
}
</script>

<style lang="scss">
.recommend {
  @include center($head-h, $base-h);
   display: flex;
  flex-direction: column;
  font-family:PingFang-SC-Medium;
  .logo{
  position: fixed;
  top: 0;
  bottom: 0;
  right: 0;
  left: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 3;
  background: rgba(3, 3, 3, .3);
  text-align: center;
  >div{
    display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
    width: 2.35rem;
    height: 2rem;
    border-radius: .1rem;
    background: #fff;
    >i{
      font-size: 1rem;
      color: #EFEFEF;
      display: inline-block;
    }
    >P{
      color: #E4C471;
      font-size: $title-f;
    }
  }
  }

  .banner-logo{
    height: 1.5rem;
    background: $base-bg;
    padding-bottom: .1rem;
    img{
      width: 100vw;
      height: 1.5rem
    }
  }
  .list-main{
    flex: 1 0;
    align-items: center;
    width: 80%;
  }
  .list-box{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
  .title{
    width: 100%;
    display: flex;
    justify-content: space-between;
    height: .4rem;
    >p{
      font-size: $title-f;
      color:#333;
    }
  }
  .mark{
    width: .22rem;
    line-height: .29rem;
    height: .29rem;
    font-size: $sf;
    color: #fff;
    background: url(../../assets/money/mark.png) no-repeat;
    background-size: 100%;
    text-align: center;
  }
  .list{
    display: flex;
    flex-direction: column;
    align-items: center;
    margin: .33rem 0 0 0;
    width: 1.94rem;
    height: 1.94rem;
    background: url(../../assets/rem/黄.png) no-repeat;
    background-size: 100%;
    font-size: $sf;
    color: #999;
    .nian{
      margin-top: .5rem;
    }
    .li{
      font-size: .35rem;
      color: #D8A955;
    }
    .foot{
      display: flex;
      justify-content: center;
      align-items: center;
      >div{
      min-width: .7rem;
      text-align: center;
      }
    }
  }
}
.main{
    background: #333;
}
.xing{
  position: absolute;
  top: 1.5rem - .3rem;
  left: 50%;
  margin-left: -.25rem;
}
.tou{
  position: absolute;
  bottom: .05rem;
  left: .15rem;
  width: calc(100vw - .16rem - .16rem);
  line-height: .44rem;
  color: #fff;
  z-index: 1;
}
</style>



