<template>
    <div class="body">
        <ul>
      <li v-if="rex.realName">
           <p>姓名</p>
           <p>{{rex.realName|real3}}</p>
       </li>
      <li v-if="!rex.realName" >
           <p>姓名</p>
           <div @click="wei">
               <p>未认证</p>
               <i class='fa fa-angle-right'></i>
               </div>
       </li>
      <li v-if="rex.idCard">
           <p>身份证</p>
           <p>{{rex.idCard|id18}}</p>
       </li>
        <li v-if="!rex.idCard">
           <p>身份证</p>
           <div @click="wei">
               <p>未认证</p>
               <i class='fa fa-angle-right'></i>
               </div>
       </li>
      <li>
           <p>绑定手机</p>
           <p>{{rex.phoneNumber}}</p>
       </li>
      <li @click="go('myBank')">
           <p>默认银行卡</p>
           <div>
               <p>{{rex.defaultcard}}</p>
               <i class='fa fa-angle-right'></i>
               </div>
       </li>
      <li @click="go('myEmail')">
           <p>电子邮箱</p>
           <div>
               <p>{{rex.email}}</p>
               <i class='fa fa-angle-right'></i>
               </div>
       </li>
      <li @click="go('myAddres')">
           <p>详细地址</p>
           <div>
               <p>{{rex.address|address10}}</p>
               <i class='fa fa-angle-right'></i>
               </div>
       </li>
      <li @click="go('myCode')">
           <p>登录密码</p>
           <i class='fa fa-angle-right'></i>
       </li>
      <li @click="go('myHand')">
           <p>手势密码</p>
           <i class='fa fa-angle-right'></i>
       </li>
        </ul>
        <div class="kefu">
       <ke-fu></ke-fu>
        </div>
    </div>
</template>

<script>
import keFu from './myKe'
export default {
  data () {
    return {
      rex: {}
    }
  },
  methods: {
    go: function (url) { // 跳转函数
      console.log(11)
      if (url === 'myEmail' || url === 'myAddres') {
        this.$router.push({path: '/my/message/' + url})
      } else {
        if (url === 'myHand') {
          this.$router.push({
            name: url,
            params: {id: 'change'}
          })
        } else {
          this.$router.push({name: url})
        }
      }
    },
    wei: function () {
      this.$router.push({name: 'myReal'})
    }
  },
  filters: {
    real3: function (value) { // 姓名过滤
      if (value) {
        return value.substr(0, 1) + '*' + value.substr(2, 1)
      }
    },
    address10: function (value) {
      if (value) {
        return value.substr(0, 10) + '...'
      }
    }
  },
  components: {
    keFu
  },
  beforeCreate () {
    this.$parent.head = {
      isWhite: false, // 箭头和标题颜色
      isEnter: false, // 标题字体
      title: '账户设置', // 标题内容
      isArrow: false, // 箭头显隐
      back: () => { // 点击返回
        this.$router.back(-1)
      }
    }
  },
  created () {
    let a = localStorage.getItem('id')
    this.$http(this.path.myUse(a)) // 页面请求
      .then((rex) => {
        console.log(rex)
        this.rex = rex.data.data
      })
  }
}
</script>

<style lang="scss" scoped>
.body{
    @include center($head-h,0);
    background: $base-bg;
    ul{
      margin-top: .1rem;
      li{
          display: flex;
          justify-content:space-between;
          align-items: center;
          height: .6rem;
          padding: 0 .15rem;
          font-family: PingFang-SC-Medium;
          background: #fff;
          >p{
              &:nth-child(1){
              width: .75rem;
              color: #333333;
              font-size: $mf;
              text-align-last: justify;
              }
              &:nth-child(2){
                  font-size: $mf;
                  color: #999999;
              }
          }
          >div{
            display: flex;
            align-items: center;
             font-size: $mf;
            color: #999999;
            >p{
                margin-right: .1rem;
            }
          }
          i{
            color: #999999;
            font-size: .14rem;
          }
      }
    }
    .kefu{
      margin: .55rem 0 .1rem 0;
      text-align: center;
    }
}
</style>



