<template>
  <div>
    <header class="head-view container head-style bgwh" v-show="head.isHead" :class="{'head-orange':head.isWhite}">
      <span class="head-title" :class="{'enter-title':head.isEnter}">{{head.title}}</span>
      <router-link to="/my/myMessage"  class="fa fa-bell-o tr" :class="{'vh':head.isBell}"></router-link>
    </header>
    <router-view></router-view>
    <footer class="main-foot">
        <button @click="footNav('isRecome', 'main.recommend')" :class="{true:'isRecome',false:'recome'}[foot.isRecome]">
        <i></i>
        <p>推荐</p>
      </button>
      <button @click="footNav('isMoney','main.money')" :class="{true:'isMoney',false:'money'}[foot.isMoney]">
        <i></i>
        <p>理财</p>
      </button>
      <button @click="footNav('isInsvet','main.invest')" :class="{true:'isInsvet',false:'insvet'}[foot.isInsvet]">
        <i></i>
        <p>投资</p>
      </button>
      <button @click="footNav('isMy','main.my')" :class="{true:'isMy',false:'my'}[foot.isMy]">
        <i></i>
        <p>我的</p>
      </button>
    </footer>
  </div>
</template>

<script>
export default {
  data () {
    return {
      head: {
        isHead: true, // 头部显隐
        isWhite: false, // 箭头和标题颜色
        isEnter: false, // 标题字体
        title: '精品推荐', // 标题内容
        isBell: false // 铃铛显隐
      },
      foot: {
        isRecome: false, // 推荐页面
        isMoney: false, // 理财页面
        isInsvet: false, // 投资页面
        isMy: false // 我的页面
      }
    }
  },
  methods: {
    footNav: function (value, url) { // 点击函数
      for (let i in this.foot) { // 循环遍历对象属性
        if (i === value) { // 传入的变成true
          this.foot[i] = true
        } else {
          this.foot[i] = false
        }
        this.$router.push({
          name: url
        })
      }
    }
  }
}
</script>

<style lang='scss'>
.head-view {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    line-height: $head-h;
    font-family: "PingFang-SC-Medium";
    text-align: center;
    z-index: 1;
    a{
        color: $title-color;
    }
    .fa {
        float: right;
        line-height: $head-h;
        font-size: .2rem;
    }
    .fa-arrow-left {
      -webkit-text-stroke: 0.01rem #fff;
    }
    .head-title {
      font-size: $head-f;
    }
    .enter-title {
      font-size: $enter-f;
    }
    .arrow-wh {
      color: #fff;
      -webkit-text-stroke: 0.01rem $title-color;
    }
  }

  .head-style {
    //顶部栏高度50px,字体橘色
    height: $head-h;
    color: $title-color;
  }
  .head-orange {
    //头部背景变橘色，字体变白
    background: #D6A956;
    color: #fff;
  } // =================
  .main-foot {
    display: flex;
    justify-content: space-around;
    position: fixed;
    left: 0;
    right: 0;
    bottom: 0;
    height: $base-h;
    background: #fff;
    border-top: 1px solid $border-color;
    z-index: 2;
    >button {
      flex: 0 0 25%;
      outline-color: transparent;
      p {
        font-size: $foot-f;
        color: $tip-color;
      }
      i::before {
        content: "";
        display: inline-block;
        width: 0.3rem;
        height: 0.3rem;
        margin-top: 0.05rem;
      }
    }
    .recome {
      i::before {
        background: url(../assets/foot/recom.png) no-repeat;
        background-size: 100%;
      }
    }
    .money {
      i::before {
        height: 0.35rem;
        margin-top: 0;
        background: url(../assets/foot/money.png) no-repeat;
        background-size: 100%;
      }
    }
    .insvet {
      i::before {
        background: url(../assets/foot/invest.png) no-repeat;
        background-size: 100%;
      }
    }
    .my {
      i::before {
        background: url(../assets/foot/my.png) no-repeat;
        background-size: 100%;
      }
    }
    .isRecome {
      p {
        color: $title-color;
      }
      i::before {
        background: url(../assets/foot/recom-touch.png) no-repeat;
        background-size: 100%;
      }
    }
    .isMoney {
      p {
        color: $title-color;
      }
      i::before {
        margin-top: 0;
        height: 0.35rem;
        background: url(../assets/foot/money-touch.png) no-repeat;
        background-size: 100%;
      }
    }
    .isInsvet {
      p {
        color: $title-color;
      }
      i::before {
        background: url(../assets/foot/inves-touch.png) no-repeat;
        background-size: 100%;
      }
    }
    .isMy {
      p {
        color: $title-color;
      }
      i::before {
        background: url(../assets/foot/my-touch.png) no-repeat;
        background-size: 100%;
      }
    }
  }

</style>



