<template>
  <div>
    <header class="head-view container head-style"  >
    <i class="fa fa-arrow-left "  @click="back()"></i>
    <p class="head-title" >{{head.title}}</p>
    </header>
    <router-view></router-view>
  </div>
</template>

<script>
export default {
  data () {
    return {
      head: {
        title: '登录'// 标题内容
      }
    }
  },
  methods: {
    back: function () { // 点击返回
      this.$router.back(-1)
    }
  }
}
</script>

<style lang='scss' scoped>
.head-view {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    line-height: $head-h;
    font-family: "PingFang-SC-Medium";
    text-align: center;
    z-index: 1;
    background: #eeeef3;
    .fa-arrow-left {
        float: left;
        line-height: $head-h;
        font-size: $enter-f;
        color: #c39d71;
    }
    .head-title {
        font-size: $head-f;
        color: #c39d71;
    }
    .enter-title {
        font-size: $enter-f;
    }
}

.head-style {
    //顶部栏高度50px,字体橘色
    height: $head-h;
    color: $title-color;
}

.head-orange {
    //头部背景变橘色，字体变白
    background: $title-color;
    color: #fff;
}
</style>



