<template>
    <div class="contract-view bte8 container">
        <transition name="slide-fade">
            <my-canvas class="bte8" :writeShow='writeShow' v-if="writeShow"></my-canvas>
        </transition>
        <borrow-contract :contractShow='contractShow' :contract='contract' :today='today'></borrow-contract>
        <out-contract :contractShow='contractShow' :contract='contract' :underLine='underLine'></out-contract>
        <litigation-contract :contractShow='contractShow' :contract='contract'></litigation-contract>
        <principal-contract :contractShow='contractShow' :contract='contract' :money='money' :underLine='underLine'></principal-contract>
        <br>
        <div class="tr" v-show="!contractShow.isPrincipal">
            <span> 甲方（电子签章）：</span>
            <button class="userSign userSign-bg" v-show="isUserSign" :disabled="writeAble" @click="goWrite()">签字区</button>
            <img :src="contract.userSign" class="userSign tc" v-show="!isUserSign" >
        </div>
        <br>
        <div class="tr">
            <span>乙方（电子签章）：</span>
            <img :src="contract.companyCachet" alt="" class="comSign">
        </div>
        <br>
        <p class="date tr" v-if="contractShow.isPrincipal">日期：
            <span v-if="!underLine">{{contract.contractCreateAt|dateText}}</span>
            <span v-if="underLine">XXXX年XX月XX日</span>
        </p>
        <div class="confirm-box bte8" v-if="isBtn">
            <button :class="[{'btn-grey':isContinue}, 'btn']" @click="confirm()" :disabled="isContinue">确定</button>
        </div>
        <transition name='fade'>
            <span class="msg" v-if="isMsg">续投已受理，请静待进行中投资到期</span>
        </transition>
        <transition name="slide-fade">
            <div class="write-alert alert flex-cent" @click.self="boxHide()" v-if="isWrite">
                <ul class="tc">
                    <li class="write-one bbe8">提示</li>
                    <li class="write-two">单击甲方电子签章区域签字</li>
                    <li class="write-three" @click="writeConfirm()">确认</li>
                </ul>
            </div>
        </transition>
    </div>
</template>

<script>
import borrow from './borrow'
import out from './out'
import litigation from './litigation'
import principal from './principal'
import write from './write'
export default{
  data () {
    return {
      contract: {},
      id: undefined,
      isUserSign: true, // 签名灰色背景
      writeAble: true, // 能否点击签名
      writeShow: false, // 签名显示隐藏
      isWrite: false, // 签名提示弹窗显隐
      isContinue: false, // 续投成功
      transcationId: undefined, // 交易id
      isMsg: false, // 提示信息
      type: undefined, // 合同类型
      money: '', //  金额
      confirm: undefined, // 确定按钮函数
      tody: '', // 今天日期
      params: {}, // 发送数据
      payMsg: {}, // 富有支付发送数据
      underLine: true, // 是否为空白合同
      isBtn: false, // 按钮显示
      contractShow: { // 合同
        isBorrow: false, // 出借协议
        isOut: false, // 转让
        isLitigation: false, // 诉讼
        isPrincipal: false // 出借本金
      }
    }
  },
  created () {
    this.today = new Date().getTime()
    this.getContract()
    switch (this.type) {
      case 2:
        this.contractShow.isBorrow = true
        break
      case 3:
        this.contractShow.isOut = true
        break
      case 4:
        this.contractShow.isLitigation = true
        break
      case 5:
        this.contractShow.isPrincipal = true
    }
    if (this.type && this.id === undefined) { // 未签署
      this.isUserSign = true // 显示灰色签名区
      this.writeAble = true // 不可点击签名
      this.$http(this.path.contractNot(this.type)).then(res => { // 合同type
        console.log(res.data.code)
        if (res.data.code === 0) {
          this.contract = res.data
          this.underLine = true // 显示空白合同格式
        }
        console.log(this.contract, '未签署')
      })
    } else if (this.id && this.type | this.transcationId) { // 已签署或者续投
      this.isUserSign = false // 灰色签名消失
      this.$http(this.path.contractHave(this.id)).then(res => { // 合同id
        console.log(res.data.code)
        if (res.data.code === 0) {
          this.contract = res.data.data
        }
        console.log(this.contract, '已签署')
        this.params = {id: this.transcationId, userSign: this.contract.userSign}
        console.log(this.params)
      })
      if (this.transcationId) { // 如果是续投
        this.isBtn = true // 底部按钮显示
        this.confirm = () => { // 点击确定添加续投
          this.$http(this.path.continueContract(this.params)).then(res => { // 交易id和签名
            console.log(res.data, '已续投')
            this.isContinue = true // 按钮置灰
            this.isMsg = true // 提示信息
            setTimeout(
              () => { this.isMsg = false }, 4000)
          })
        }
      } else { // 如果是已签署
        this.isBtn = false // 底部按钮消失
      }
    } else { // 投资待签署
      this.isBtn = true // 底部按钮显示
      this.isUserSign = true // 显示灰色签名区
      this.writeAble = false // 可点击签名区
      this.contractShow.isBorrow = true // 出借协议显示
      this.isWrite = true // 签名提示窗
      console.log('投资待签署')
      this.$http(this.path.contractWait).then(res => {
        console.log(res.data.code)
        if (res.data.code === 0) {
          this.contract = res.data.data
          console.log(this.contract)
        }
      })
      console.log(this.contract.userSign)
      if (this.contract.userSign === undefined) { // 如果没有用户签名
        this.confirm = () => { // 点击确定弹窗
          this.isWrite = true
        }
      }
    }
    this.$parent.head.title = '合同详情'
    this.$parent.head.isWhite = false
    this.$parent.head.isEnter = false
    this.$parent.head.isArrow = false
  },
  methods: {
    boxHide () { // 点击灰色区域弹窗消失
      this.isWrite = false
    },
    writeConfirm () { // 点击确定区域弹窗消失
      this.isWrite = false
    },
    getContract () { // 获得id
      this.id = this.$route.query.id
      this.type = Number(this.$route.query.type)
      this.money = this.$route.query.money
      this.transcationId = this.$route.query.transcationId
      this.payMsg = this.$route.params
      console.log('合同类型' + this.type, 'id' + this.id, '金额' + this.money, '交易id' + this.transcationId)
      console.log(this.contractShow)
      console.log(this.$route.query)
      console.log(this.$route.params)
    },
    goWrite () {
      this.writeShow = true
    }
  },
  components: {
    'borrow-contract': borrow,
    'out-contract': out,
    'litigation-contract': litigation,
    'principal-contract': principal,
    'my-canvas': write
  }
}

</script>

<style lang="scss" scoped>
.contract-view {
  @include center($head-h, 0);
  padding-bottom: 0.65rem;
  font-family: "Microsoft YaHe";
  line-height: 0.2rem;
  font-size: 0.1rem;
  .comSign {
    width: 1rem;
  }
  .userSign {
    display: inline-block;
    width: 1rem;
  }
  .userSign-bg{
     line-height: .3rem;
     background: #ccc;
  }
  .date {
    padding-right: 0.92rem;
  }
  .confirm-box {
    position: fixed;
    padding: 0.1rem;
    bottom: 0;
    left: 0;
    right: 0;
    background: #fff;
    .btn {
      width: 100%;
    }
  }
  .msg {
    position: fixed;
    bottom: 0.7rem;
    left: 50%;
    width: 2.1rem;
    transform: translateX(-50%);
  }
  .write-alert {
    .tc {
      width: 3rem;
      background: #fff;
      border-radius: 10px;
      .write-one {
        line-height: 0.5rem;
        font-size: 0.17rem;
        font-family: "Adobe Heiti Std";
      }
      .write-two {
        line-height: 0.7rem;
        color: $tip-color;
      }
      .write-three {
        line-height: 0.4rem;
        font-size: 0.17rem;
        color: #fff;
        background: #e4c471;
        border-radius: 0 0 10px 10px;
      }
    }
  }
}
</style>



